import { Lead } from 'src/app/_models/Lead';
import { Process } from './../../_models/Process';
import { GridOptions } from 'ag-grid-community';
import { Router } from '@angular/router';
import { AddProcessComponent } from './../addProcess/addProcess.component';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { BusinessService } from 'src/app/services/business.service';


@Component({
  selector: 'app-process',
  templateUrl: './process.component.html',
  styleUrls: ['./process.component.css']
})
export class ProcessComponent implements OnInit {
arrProcesses: any;
unSortedProcesses: any;
public rowSelection;
  public defaultColDef;
  public overlayNoRowsTemplate;
gridOptions: GridOptions = { };
columnDefs = [
  {headerName: 'Name', field: 'name', sortable: true, filter: true },
  // {headerName: 'Step', field: 'step', sortable: true, filter: true }
];

  constructor(private httpService: HttpClient, private router: Router, public dialog: MatDialog,
    private businessService: BusinessService) {
      this.rowSelection = 'single';
     this.defaultColDef = { resizable: true };

     this.overlayNoRowsTemplate =
     '<span class="ag-overlay-loading-center">Please wait while your rows are loading</span>';
     }

  ngOnInit() {
    this.httpService.get('./../../../assets/process.json').subscribe(
      data => {
        this.arrProcesses = data as Process[];
        this.unSortedProcesses = data;
        console.log(this.arrProcesses);
        this.gridOptions.api.setRowData(this.arrProcesses);
      },
      (err: HttpErrorResponse) => {
        console.log (err.message);
      }
    );
  }

  ngAfterViewInit() {
    this.gridOptions.api.setColumnDefs([
    {headerName: 'Name', field: 'name', sortable: true, filter: true },
    {headerName: 'Step', field: 'steps', sortable: true, filter: true},
    ]);

    this.gridOptions.api.sizeColumnsToFit();
  }

  addProcess() {
    this.dialog.open(AddProcessComponent, {
      width: '550px',
     });
  }

  openProcess(process) {
    localStorage.setItem('selectedProcess', JSON.stringify(process));
    this.router.navigate(['/viewProcess']);
  }

  onSelectionChanged(event) {
    const selectedRows = this.gridOptions.api.getSelectedRows();
    console.log(selectedRows);
      // this.dialog.open(InventoryEditComponent, {
      //  width: '450px',
      //   data: {data: selectedRows[0], activateEditBtn: true}
      // });
  }

}
