import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';

@Component({
  selector: 'app-enterComponentsQuantity',
  templateUrl: './enterComponentsQuantity.component.html',
  styleUrls: ['./enterComponentsQuantity.component.css']
})
export class EnterComponentsQuantityComponent implements OnInit {
  form: FormGroup;
  subAssemblies = ['Sub assembly 1','Sub assembly 2','Sub assembly 3','Sub assembly 4']
  components = ['Component 1', 'Component 2', 'Component 3'];

  constructor(public fb: FormBuilder, public fbItem: FormBuilder) { 
    this.form = fb.group({
      'name' : [null, Validators.required],
      'producedQuantity' : [null, Validators.required],
      'output' : [null, Validators.required],
      'waste' : [null, Validators.required],
      'startDate' : [null, Validators.required],
      'subAssembly' : [null, Validators.required]
   });
  }

  ngOnInit() {
    this.form = this.fb.group({
      'name' : [null, Validators.required],
      'producedQuantity' : [null, Validators.required],
      'output' : [null, Validators.required],
      'waste' : [null, Validators.required],
      'startDate' : [null, Validators.required],
      'subAssembly' : [null, Validators.required],
      components: this.fb.array([])
    });
    for(let i=0; i< this.subAssemblies.length-1; i++) {
      this.addNext(this.components[i]);
    }
  }

  createItem(componentName: string) {
    return this.fbItem.group({
      name: [componentName],
      producedQuantity: ['']
    });
  }

  addNext(componentName: string) {
    (this.form.controls['components'] as FormArray).push(this.createItem(componentName));
  }

  save() {
    console.log(this.form.value);
  }

}
