import { MatDialogRef } from '@angular/material';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormArray } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/authentication/user.service';

@Component({
// tslint:disable-next-line: component-selector
  selector: 'app-addRawMaterial',
  templateUrl: './addRawMaterial.component.html',
  styleUrls: ['./addRawMaterial.component.css']
})
export class AddRawMaterialComponent implements OnInit {
  public rowData: any;
  public inventoryData: any;
  public inventoryItemData: any;
  private getInventoryURL = 'http://sm.clients.ravinderpayal.com/v1/inventory/';
  private getInventoryItemsURL = 'http://sm.clients.ravinderpayal.com/v1/inventoryitem/';
  form: FormGroup;
  outputForm: FormGroup;
  leadFilters = ['Lead', 'Aluminium', 'Zinc', 'Copper'];

  constructor(private fb: FormBuilder, private formBuilder: FormBuilder, private router: Router,
    public userService: UserService, public dialogRef: MatDialogRef<AddRawMaterialComponent>) { }

  ngOnInit() {
    this.form = this.fb.group({
      items: this.fb.array([this.createItem()])
    });
    this.outputForm = this.formBuilder.group({
      outputItems: this.formBuilder.array([this.createOutputItem()])
    });
    this.getInventory();
  }

  getInventoryName(inventoryId) {
    return  this.inventoryItemData.filter(inventoryData => inventoryData.id === inventoryId)[0].name;

  }

  getInventory() {
    this.userService.authenticatedGet(this.getInventoryURL, {}).subscribe((res: any) => {
      this.rowData = res.data;
      console.log(this.rowData);
      this.finalCombine();
    });

  }

  closeDialog() {
    this.dialogRef.close();
  }

  finalCombine() {
    this.userService.authenticatedGet(this.getInventoryItemsURL, {}).subscribe((res: any) => {
    this.inventoryItemData = res.data;

    this.inventoryData = this.rowData.map( inventory => {
        return {id: inventory.id, itemId: inventory.itemId, quantityAmount: inventory.quantityAmount ,
          quantityUnit: inventory.quantityUnit, name: this.getInventoryName(inventory.itemId)};
    });
    this.leadFilters = [];
    for (var i = 0, len = this.inventoryData.length; i < len; ++i) {
      var inventory: any = this.inventoryData[i];
      this.leadFilters.push(inventory.name);
  }
    });
  }

  createItem() {
    return this.fb.group({
      name: [''],
      scrapFactor: ['']
    });
  }

  createOutputItem() {
    return this.formBuilder.group({
      newName: [''],
    });
  }

  addNext() {
    (this.form.controls['items'] as FormArray).push(this.createItem());
  }

  addNextOutput() {
    (this.outputForm.controls['outputItems'] as FormArray).push(this.createOutputItem());
  }

  deleteRow(index: number) {
    (this.form.controls['items'] as FormArray).removeAt(index);
  }

  deleteOutputRow(index: number) {
    (this.outputForm.controls['outputItems'] as FormArray).removeAt(index);
  }

  saveAll() {
    localStorage.setItem('itemData', JSON.stringify(this.form.value));
    console.log(this.outputForm.value);
    localStorage.setItem('outputData', JSON.stringify(this.outputForm.value));
    this.router.navigate(['/editQuantity']);
  }

}
