import { Component, OnInit, Input } from '@angular/core';
import { UserService } from '../../../services/authentication/user.service';
import { ApiUrls } from '../../../services/api.urls';
import { MemCacheService } from '../../../services/cache/mem-cache.service';

@Component({
  selector: 'app-print-constituency',
  templateUrl: './print-constituency.component.html',
  styleUrls: ['./print-constituency.component.css']
})
export class PrintConstituencyComponent implements OnInit {
  public name = '';
  private stateConstituencyId;
  public loading = false;
  @Input('stateConstituencyId') set _stateConstituencyId(val: String) {
    if (val === '') {
      this.stateConstituencyId = '';
      this.name = 'Not available';
    } else if (this.stateConstituencyId !== val) {
      this.stateConstituencyId = val;
      this.load();
    }
  }
  constructor(
    private userService: UserService,
    private memCache: MemCacheService
  ) {}

  ngOnInit() {}

  load() {
    this.loading = true;
    this.memCache
      .getItem('stateconstituency', this.stateConstituencyId).map((d) => {
        this.loading = false;
        return d;
      }).catch((d) => {
        this.loading = false;
        return d;
      }).subscribe(
        data => {
          this.name = data.name;
        },
        err => {
          this.name = 'Not available';
        }
      );
  }
}
