import { InventoryService } from './../../services/inventory.service';
import { UserService } from 'src/app/services/authentication/user.service';
import { Component, OnInit, Inject } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatSnackBar, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { BusinessService } from 'src/app/services/business.service';
import { ApiUrls } from 'src/app/services/api.urls';

@Component({
  selector: 'app-RegisterInventoryCategory',
  templateUrl: './RegisterInventoryCategory.component.html',
  styleUrls: ['./RegisterInventoryCategory.component.css']
})
export class RegisterInventoryCategoryComponent implements OnInit {
  public registrationForm: FormGroup;
  pageTitle = 'Add a new inventory category';
  buttonTitle = 'Register';
  editMode = false;

  constructor(public fb: FormBuilder,
     public userService: UserService, private snackBar: MatSnackBar, private inventoryService: InventoryService,
     @Inject(MAT_DIALOG_DATA) public rowData: any, public dialogRef: MatDialogRef<RegisterInventoryCategoryComponent>) {
    this.registrationForm = fb.group({
      'name' : [this.rowData.data.name, Validators.required]
   });
  }

  ngOnInit() {
    if (this.rowData) {
      this.pageTitle = 'Edit inventory category';
      this.buttonTitle = 'Save';
      this.editMode = !this.editMode;
    }
  }

  registerLead(value: any, form): void {
    if (this.editMode) {
      console.log(this.registrationForm.value);
      this.userService.authenticatedPatch(
        ApiUrls.FromModelById('inventorycategory', this.rowData.data.id),
        JSON.stringify({
          name: this.registrationForm.value.name
        }), {}).subscribe((res) => {
      if (res.success) {
        // this.close();
        // this.rowData.data.meta = this.authForm.value.meta;
        this.snackBar.open('Updated', 'X', {
          duration: 2000,
        });
        this.inventoryService.emitInventoryCategoryEdit(this.registrationForm.value.name);
        localStorage.setItem('editedCategory',
        JSON.stringify({ id: this.rowData.data.id, name: this.registrationForm.value.name}));
        this.dialogRef.close();
        console.log(localStorage.getItem('editedCategory'));
      } else {
        this.snackBar.open('Error: ' + res.error, 'X', {
          duration: 2000,
        });
        this.dialogRef.close();
      }
    });

    } else {
   //  console.log(this.registrationForm.value);
   this.userService.authenticatedPost(ApiUrls.GetModel('inventorycategory') , JSON.stringify(this.registrationForm.value), {}).subscribe(
    res => {
      this.snackBar.open('Lead Added !', 'X', {
        duration: 4000,
        panelClass: ['success-snackbar'],
        verticalPosition: 'top'
      });
      this.inventoryService.emitProperties(this.registrationForm.value);
      this.registrationForm.reset();
      this.dialogRef.close();

    },
    err => {
      console.log('Error occured');
      console.log(err);
      this.snackBar.open('Lead creation failed !', 'X', {
        duration: 4000,
        panelClass: ['error-snackbar'],
        verticalPosition: 'top'
      });
    }
  );
    }
  }

  closeDialog() {
     this.dialogRef.close();
  }

}
