import { InventoryService } from './../../services/inventory.service';
import { UserService } from 'src/app/services/authentication/user.service';
import { Component, OnInit, Injectable } from '@angular/core';
import { InventoryItem } from '../../_models/InventoryItem';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {MatSnackBar, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs/Rx';
import {map, startWith} from 'rxjs/operators';
import { ApiUrls } from 'src/app/services/api.urls';



@Component({
  selector: 'app-RegisterInventory',
  templateUrl: './RegisterInventory.component.html',
  styleUrls: ['./RegisterInventory.component.css']
})
export class RegisterInventoryComponent implements OnInit {
  myControl = new FormControl();
  units = ['Gram', 'Kilogram', 'Litre'];
  options: string[] = ['One', 'Two', 'Three'];
  filteredOptions: Observable<InventoryItem[]>;
  public registrationForm: FormGroup;
  public inventoryItem: InventoryItem;
  public inventoryItemId: number;
  public inventoryItemList: InventoryItem[] = [];
  public state: string;
  public httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };
  public selectedInventoryItem: InventoryItem;

  constructor(public fb: FormBuilder , private userService: UserService, private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<RegisterInventoryComponent>, private inventoryService: InventoryService) {
    this.registrationForm = fb.group({
      'itemId' : [null, Validators.required],
      'quantityAmount' : [null, Validators.required],
      'quantityUnit' : [null, Validators.required],
   });
  }

  registerInventoryItem(value: any, form): void {
    const inventoryNewItem: InventoryItem = this.registrationForm.value;
    console.log(this.registrationForm.value);
    this.userService.authenticatedPost(ApiUrls.GetModel('inventory'),
      JSON.stringify({...Object.keys(this.registrationForm.value).filter(a =>
        this.registrationForm.value[a] !== null && this.registrationForm.value[a] !== '')
       .map(a => {
         return {[a] : this.registrationForm.value[a]};
       }).reduce((a, b) => {
         return {...a, ...b};
       }), roles: [this.registrationForm.value.role]})
      , {}).subscribe(res => {
        this.snackBar.open('Inventory successfully created !', 'X', {
          duration: 4000,
          panelClass: ['success-snackbar'],
          verticalPosition: 'top'
        });
        this.inventoryService.emitInventory(this.registrationForm.value);
        this.registrationForm.reset();
        form.resetForm();
        this.closeDialog();
      },
      err => {
        console.log('Error occured');
        console.log(err);
        this.snackBar.open('Inventory creation failed !', 'X', {
          duration: 4000,
          panelClass: ['error-snackbar'],
          verticalPosition: 'top'
        });
      });

  }
  ngOnInit() {
    this.filteredOptions = this.myControl.valueChanges
      .pipe(
        startWith(''),
        map(value => this._filter(value))
      );
      this.getInventoryItems();
  }
  private _filter(value: string): InventoryItem[] {
    const filterValue = value.toLowerCase();
    return this.inventoryItemList.filter
    (inventory => inventory.name.toLowerCase().indexOf(filterValue) === 0);
  }

  getInventoryItems() {
    this.userService.authenticatedGet(ApiUrls.GetModel('inventoryitem'), {}).subscribe((res: any) => {
      this.inventoryItemList = res.data;
     });
  }

  closeDialog() {
    this.dialogRef.close();
  }

  public onStateChange(argValue) {
     this.selectedInventoryItem =  this.inventoryItemList.filter
     (inventory => inventory.name.toLowerCase().indexOf(argValue.toLowerCase()) === 0)[0];
     console.log(this.selectedInventoryItem);
     this.inventoryItemId = this.selectedInventoryItem.id;
  }

}
