import { Injectable } from "@angular/core";
import { MatSnackBar } from "@angular/material";

@Injectable()
export class HandleGlobalErrorService {
  unchekedError = [];
  constructor(private snackBar: MatSnackBar) {}
  error(context, error, button = null, time = 2500) {
    const err = { error: context + " : " + error, button, time };
    this.unchekedError.push(err);
    this.snackBar
      .open(err.error, "", {
        duration: time
      })
      .afterDismissed()
      .subscribe(() => {
        this.removeError(err);
      });
  }
  removeError(err) {
    const index = this.unchekedError.indexOf(err);
    this.unchekedError.splice(index, 1);
  }
}

export class GlobalErrorCodes {
  static ServerNoResponse =
    "Blank server response. Check your internet connection";
  static ServerActionNotFound = "This action is not available on server";
  static ServerActionForbidden = "Action not available to you";
  static ServerDown = "Server is either down or not working properly";
  static ServerNotAccessible =
    "Server is not accessible, check your internet connection";
  static NoInterNet = "Internet connection not available";
}
