import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormArray } from '@angular/forms';
import { MatDialogRef, MatSnackBar, MatDialog } from '@angular/material';
import { AddAssemblyComponent } from '../AddAssembly/AddAssembly.component';
import { AddSubAssemblyComponent } from '../AddSubAssembly/AddSubAssembly.component';

@Component({
  selector: 'app-configureDesign',
  templateUrl: './configureDesign.component.html',
  styleUrls: ['./configureDesign.component.css']
})
export class ConfigureDesignComponent implements OnInit {
  assemblyName: string;
  form: FormGroup;
  

  constructor(public fb: FormBuilder, public dialogRef: MatDialogRef<ConfigureDesignComponent>,
    private snackBar: MatSnackBar, private dialog: MatDialog, private fbItem: FormBuilder) { }

  ngOnInit() {
    this.form = this.fb.group({
      assemblies: this.fb.array([this.createItem()])
    });
  }

  addAssembly() {
    this.dialog.open(AddSubAssemblyComponent, {
      width: '550px',
     });
  }


   deleteRow(index: number) {
    (this.form.controls['assemblies'] as FormArray).removeAt(index);
  }

  addNext() {
    (this.form.controls['assemblies'] as FormArray).push(this.createItem());
  }

  createItem() {
    return this.fbItem.group({
      assembly: ['']
    });
  }

  closeDialog() {
    this.dialogRef.close();
  }

  addSubAssembly(item: any) {
    localStorage.setItem('assemblyName', item.value.assembly);
    this.addAssembly(); 
   }

   saveAssembly() {
     console.log(this.form.value);
   }

}
