import { RegisterComponent } from '../Register/Register.component';
import { Component, OnInit, Inject } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';

import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import {MatSnackBar} from '@angular/material';

import { UserService, AppHttpHandledError, AppServerResponse } from '../../services/authentication/user.service';
import { ApiUrls } from 'src/app/services/api.urls';

declare var require: any;

const dot = require('dot-object');


@Component({
  selector: 'app-InventoryItem-edit',
  templateUrl: './InventoryItem-edit.component.html',
  styleUrls: ['./InventoryItem-edit.component.css']
})
export class InventoryItemEditComponent implements OnInit {
  data: any = {
    name: '',
    barcode: '',
    category: '',
    dimensionType: '',
    id: '',
    model: '',
    saleable: '',
    stateBoothId: '',
    time: '',
  };
  public registrationForm: FormGroup;
  private registerInventoryItemURL = 'http://sm.clients.ravinderpayal.com/v1/inventoryitem/';

  constructor(private modalService: NgbModal, public apiUrl: ApiUrls, public formBuilder: FormBuilder,
    public userService: UserService,
    private snackBar: MatSnackBar,
    @Inject(MAT_DIALOG_DATA) public rowData: any, public dialogRef: MatDialogRef<InventoryItemEditComponent>) {
     //console.log(dot);
     if (!(isObjectEmpty(rowData.data))) {
       console.log(this.data);
       //this.userId = rowData.data.id;
       this.data = rowData.data;
     }
     function isObjectEmpty(Obj) {
       for (const key in Obj) {
       if (Obj.hasOwnProperty(key)) {
       return false;
       }
       }
       return true;
       }

     console.log(this.data);
   this.registrationForm = this.formBuilder.group({
     'name': [this.data.name, Validators.compose([Validators.required])],
     'category': [this.data.category, Validators.compose([Validators.required])],
     'dimensionType': [this.data.dimensionType, Validators.compose([Validators.required])],
     'model': [this.data.dimensionType, Validators.compose([Validators.required])],
     'saleable': [this.data.saleable, Validators.compose([Validators.required])],
     'barcode': [this.data.barcode, Validators.compose([Validators.required])],
     // 'roles': [null, Validators.compose([Validators.required])]
     });
   }

  ngOnInit() {
  }

  closeDialog() {
    this.dialogRef.close();
  }

  onSubmit(value: any, form): void {

      console.log(this.registrationForm.value);
      this.userService.authenticatedPatch(this.registerInventoryItemURL + this.data.id.toString(),
        JSON.stringify({... dot.dot(Object.keys(this.registrationForm.value)
          .filter(a => this.registrationForm.value[a] !== null && this.registrationForm.value[a] !== '')
          .map(a => {
            if ( this.registrationForm.value[a] === 'true' || this.registrationForm.value[a] === 'false') {
              if ( this.registrationForm.value[a] === 'true')  {
               this.registrationForm.value[a] = true;
               return {[a] : this.registrationForm.value[a]};
              } else { this.registrationForm.value[a] = false;
             return {[a] : this.registrationForm.value[a]}; }
          } else {
          return {[a] : this.registrationForm.value[a]};
          }
          }).reduce((a, b) => {
            return {...a, ...b};
          }))}), {}).subscribe(
          res => {
            this.snackBar.open('Inventory item successfully updated !', 'X', {
              duration: 4000,
              panelClass: ['success-snackbar'],
              verticalPosition: 'top'
            });
            this.registrationForm.reset();
            this.closeDialog();
          },
          err => {
            console.log('Error occured');
            console.log(err);
            this.snackBar.open('Inventory item updation failed !', 'X', {
              duration: 4000,
              panelClass: ['error-snackbar'],
              verticalPosition: 'top'
            });
          }
        );


  }
}
