import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { AddSubAssemblyComponentsComponent } from '../AddSubAssemblyComponent/AddSubAssemblyComponent.component';
import { FormGroup, FormBuilder, FormArray } from '@angular/forms';

@Component({
  selector: 'app-AddSubAssembly',
  templateUrl: './AddSubAssembly.component.html',
  styleUrls: ['./AddSubAssembly.component.css']
})
export class AddSubAssemblyComponent implements OnInit {
  newAssemblyName: string;
  assemblyName: string;
  form: FormGroup;

  constructor(public fb: FormBuilder, private dialog: MatDialog, public fbItem: FormBuilder,
    public dialogRef: MatDialogRef<AddSubAssemblyComponent>) { }

  ngOnInit() {
    this.newAssemblyName = localStorage.getItem('assemblyName');
    this.form = this.fb.group({
      subAssemblies: this.fb.array([this.createItem()])
    });
  }

  addSubAssemblyComponent() {
    this.dialog.open(AddSubAssemblyComponentsComponent,  {
      width: '750px',
      });
  }


   deleteRow(index: number) {
    (this.form.controls['subAssemblies'] as FormArray).removeAt(index);
  }

  addNext() {
    (this.form.controls['subAssemblies'] as FormArray).push(this.createItem());
  }

  createItem() {
    return this.fbItem.group({
      subAssembly: ['']
    });
  }

  closeDialog() {
    this.dialogRef.close();
  }

  addComponents(item: any) {
    console.log(item);
    localStorage.setItem('subAssemblyName', item.value.subAssembly);
    this.addSubAssemblyComponent(); 
   }


}
