import { Component, OnInit, Input } from '@angular/core';
import { UserService } from '../../../services/authentication/user.service';
import { ApiUrls } from '../../../services/api.urls';
import { MemCacheService } from '../../../services/cache/mem-cache.service';
@Component({
  selector: 'app-print-booth',
  templateUrl: './print-booth.component.html',
  styleUrls: ['./print-booth.component.css']
})
export class PrintBoothComponent implements OnInit {
  public name = '';
  private stateBoothId;
  public loading = false;
  @Input('stateBoothId') set _stateBoothId(val: String) {
    if (val === '') {
      this.stateBoothId = '';
      this.name = 'Not available';
    } else if (this.stateBoothId !== val) {
      this.stateBoothId = val;
      this.load();
    }
  }
  constructor(
    private userService: UserService,
    private memCache: MemCacheService
  ) {}

  ngOnInit() {}

  load() {
    this.loading = true;
    this.memCache.getItem('statebooth', this.stateBoothId).subscribe(
      data => {
        this.name = data.name + ` (${data.boothNumber})`;
        this.loading = false;
      },
      err => {
        this.name = 'Not available';
        this.loading = false;
      }
    );
  }
}
