import { Injectable } from "@angular/core";
import { Router, CanActivate } from "@angular/router";
import { UserService } from "./user.service";
import { MatSnackBar } from "@angular/material";

@Injectable()
export class LoggedInGuard implements CanActivate {
  constructor(private user: UserService, private _router: Router) {}

  canActivate() {
    const ret = this.user.isLoggedIn();
    console.log("It checked again " + ret);
    if (!ret) {
      this._router.navigate(["/authentication/login"]);
      return false;
    } else {
      return true;
    }
  }
}

@Injectable()
export class NonLoggedInGuard implements CanActivate {
  constructor(private user: UserService, private _router: Router) {}
  canActivate() {
    const ret = this.user.isLoggedIn();
    if (ret) {
      this._router.navigate(["/manage/"]);
      return false;
    } else {
      return true;
    }
  }
}
