import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MatSnackBar } from '@angular/material';
import { UserService } from 'src/app/services/authentication/user.service';
import { HttpHeaders } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';
import { ApiUrls } from 'src/app/services/api.urls';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'app-createProductionRun',
  templateUrl: './createProductionRun.component.html',
  styleUrls: ['./createProductionRun.component.css']
})
export class CreateProductionRunComponent implements OnInit {
  public registrationForm: FormGroup;
  public order: any;
  public httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  constructor(public fb: FormBuilder , private userService: UserService, private snackBar: MatSnackBar,
     private router: Router, private route: ActivatedRoute) {
    this.registrationForm = fb.group({
      'name' : [null, Validators.required],
      'product' : [null, Validators.required],
      'startDate' : [null, Validators.required],
      'endDate' : [null, Validators.required],
      'remarks' : [null, Validators.required],
      'stage' : ['toBeDone', Validators.required]
   });
  }


  registerInventoryItem(value: any, form): void {
    console.log(value);
    this.userService.authenticatedPost(ApiUrls.GetModel('productionmodel'), JSON.stringify(value), {}).subscribe(
      res => {
        this.snackBar.open('Production Run Added !', 'X', {
          duration: 4000,
          panelClass: ['success-snackbar'],
          verticalPosition: 'top'
        });
        this.registrationForm.reset();
      },
      err => {
        console.log('Error occured');
        console.log(err);
        this.snackBar.open('Lead creation failed !', 'X', {
          duration: 4000,
          panelClass: ['error-snackbar'],
          verticalPosition: 'top'
        });
      }
    );

  }

  saveChangesToForm(value, form) {
    this.userService.authenticatedPatch(ApiUrls.GetModel('productionmodel') + this.order.id.toString(),
        JSON.stringify(value
          ), {}).subscribe(
          res => {
            this.snackBar.open('Production successfully updated !', 'X', {
              duration: 4000,
              panelClass: ['success-snackbar'],
              verticalPosition: 'top'
            });
          },
          err => {
            console.log('Error occured');
            console.log(err);
            this.snackBar.open('Production updation failed !', 'X', {
              duration: 4000,
              panelClass: ['error-snackbar'],
              verticalPosition: 'top'
            });
          }
        );
  }

  enterDefineProductionForm() {
    localStorage.setItem('productionForm', JSON.stringify(this.registrationForm.value));
    this.router.navigate(['/defineProductionRun']);
  }

  ngOnInit() {
    this.order = JSON.parse(localStorage.getItem('editProduction'));
    if (this.order != null) {
    this.registrationForm.patchValue({
      name: this.order.name,
      remarks: this.order.remarks,
      product: this.order.product,
      startDate: this.order.startDate ,
      endDate : this.order.endDate,
      stage : this.order.stage
    });
    localStorage.removeItem('editProduction');
  }
  }

}
