import { Component, OnInit, Inject } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';

import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import {MatSnackBar} from '@angular/material';

import { UserService, AppHttpHandledError, AppServerResponse } from '../../services/authentication/user.service';

declare var require: any;

const dot = require('dot-object');

@Component({
  selector: 'app-Inventory-Edit',
  templateUrl: './Inventory-Edit.component.html',
  styleUrls: ['./Inventory-Edit.component.css']
})
export class InventoryEditComponent implements OnInit {
  data: any = {
    name: '',
    itemId: '',
    quantityAmount: '',
    quantityUnit: '',
  };
  public registrationForm: FormGroup;
  private registerInventoryURL = 'http://sm.clients.ravinderpayal.com/v1/inventory/';
  public name: string;

  constructor(private modalService: NgbModal, public formBuilder: FormBuilder,
    public userService: UserService,
    private snackBar: MatSnackBar,
    @Inject(MAT_DIALOG_DATA) public rowData: any, public dialogRef: MatDialogRef<InventoryEditComponent>) {

      if (!(isObjectEmpty(rowData.data))) {
        console.log(this.data);
        //this.userId = rowData.data.id;
        this.data = rowData.data;
        this.name = this.data.name;
      }
      function isObjectEmpty(Obj) {
        for (const key in Obj) {
        if (Obj.hasOwnProperty(key)) {
        return false;
        }
        }
        return true;
        }

      console.log(this.data);
    this.registrationForm = this.formBuilder.group({
      'itemId': [this.data.itemId, Validators.compose([Validators.required])],
      'quantityAmount': [this.data.quantityAmount, Validators.compose([Validators.required])],
      'quantityUnit': [this.data.quantityUnit, Validators.compose([Validators.required])],
      });
    }

  ngOnInit() {
  }

  closeDialog() {
    this.dialogRef.close();
  }

  onSubmit(value: any, form): void {

      console.log(this.registrationForm.value);
      this.userService.authenticatedPatch(this.registerInventoryURL + this.data.id.toString(),
        JSON.stringify({... dot.dot(Object.keys(this.registrationForm.value)
          .filter(a => this.registrationForm.value[a] !== null && this.registrationForm.value[a] !== '')
          .map(a => {
            if ( this.registrationForm.value[a] === 'true' || this.registrationForm.value[a] === 'false') {
              if ( this.registrationForm.value[a] === 'true')  {
               this.registrationForm.value[a] = true;
               return {[a] : this.registrationForm.value[a]};
              } else { this.registrationForm.value[a] = false;
             return {[a] : this.registrationForm.value[a]}; }
          } else {
          return {[a] : this.registrationForm.value[a]};
          }
          }).reduce((a, b) => {
            return {...a, ...b};
          }))}), {}).subscribe(
          res => {
            this.snackBar.open('Inventory successfully updated !', 'X', {
              duration: 4000,
              panelClass: ['success-snackbar'],
              verticalPosition: 'top'
            });
            this.registrationForm.reset();
            this.closeDialog();
          },
          err => {
            console.log('Error occured');
            console.log(err);
            this.snackBar.open('Inventory updation failed !', 'X', {
              duration: 4000,
              panelClass: ['error-snackbar'],
              verticalPosition: 'top'
            });
          }
        );


  }

}
