import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormArray } from '@angular/forms';
import { MatDialogRef, MatDialog } from '@angular/material';
import { AddRawMaterialComponent } from './../addRawMaterial/addRawMaterial.component';


@Component({
  selector: 'app-addStep',
  templateUrl: './addStep.component.html',
  styleUrls: ['./addStep.component.css']
})
export class AddStepComponent implements OnInit {

  form: FormGroup;

  constructor(public dialogRef: MatDialogRef<AddStepComponent>, private fb: FormBuilder, public dialog: MatDialog) { }

  ngOnInit() {
    this.form = this.fb.group({
      items: this.fb.array([this.createItem()])
    });
  }

  createItem() {
    return this.fb.group({
      step: ['']
    });
  }



  addNext() {
    (this.form.controls['items'] as FormArray).push(this.createItem());
  }


  deleteRow(index: number) {
    (this.form.controls['items'] as FormArray).removeAt(index);
  }

  submit() {
    console.log(this.form.value);
  }

  closeDialog() {
    this.dialogRef.close();
  }

  addRawMaterial() {
    this.dialog.open(AddRawMaterialComponent, {
      width: '750px',
     });
  }

}
