  /**
   * A wrapper of Angular 5 http request for adding Auth Token to every authenticated POST request to Server
   * @param url URL where the request to be maid
   * @param body Payload to be supplied with post request.
   * @param option
   * @returns {Observable<Response>}
   */
  authenticatedPost(
    url,
    body: object | string,
    option,
    responseStatus = OK
  ): Observable<AppServerResponse> {
    let observer: Subscriber<AppServerResponse>;
    const sub = () => {
      const authToken = localStorage.getItem('authToken');
      let headers = new HttpHeaders().set('Authorization', `${authToken}`);
      if (option.ContentType !== ' ') {
        headers = headers.append(
          'Content-Type',
          option.ContentType ? option.ContentType : 'application/json'
        );
      }
      if (option.headers) {
        option.headers.forEach(k => {
          headers = headers.append(k[0], k[1]);
        });
      }
      const mainObservable = this.http
        .post<AppServerResponse>(url, body, {
          headers: headers,
          observe: 'response'
        })
        .pipe(
          map(res => {
            if (res.status === responseStatus) {
              return res.body;
            } else if (res.status !== NOT_FOUND) {
              throw new Error(res.statusText);
            }
          }),
          catchError(this.handleHttpError.bind(this))
        );

      mainObservable.subscribe(
        res => {
          observer.next(res as AppServerResponse);
        },
        (error: AppHttpHandledError) => {
          const unsub = error.retry.subscribe(o => {
            if (o) {
              unsub.unsubscribe();
              sub();
            } else {
              if (unsub) {
                unsub.unsubscribe();
              }
              observer.error(error.error);
            }
          });
        }
      );
    };
    const retObservable: Observable<AppServerResponse> = Observable.create(
      o => {
        observer = o;
        console.log('Post is called');
        sub();
      }
    );
    return retObservable;
  }

