import { InventoryService } from './../../services/inventory.service';
import { RegisterInventoryCategoryComponent } from './../RegisterInventoryCategory/RegisterInventoryCategory.component';
import { InventoryCategory } from './../../_models/InventoryCategory';
import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MatSnackBar, MatSelectionList, MatDialog } from '@angular/material';
import { BusinessService } from 'src/app/services/business.service';
import { UserService } from 'src/app/services/authentication/user.service';
import { ApiUrls } from 'src/app/services/api.urls';
import { BusinessAddLeadComponent } from 'src/app/business/business-addLead/business-addLead.component';
import { values } from 'd3';

@Component({
  selector: 'app-Inventory-Category',
  templateUrl: './Inventory-Category.component.html',
  styleUrls: ['./Inventory-Category.component.css']
})
export class InventoryCategoryComponent implements OnInit, OnDestroy {
  public registrationForm: FormGroup;
  existingCategories: InventoryCategory[];
  existingCategory: InventoryCategory[];
  selectedList: any;
  buttonEditable = false;
  @ViewChild('list') list: MatSelectionList;

  constructor(public fb: FormBuilder, public userService: UserService, private snackBar: MatSnackBar, 
    private inventorySevice: InventoryService, private  dialog: MatDialog) {
  }

  ngOnInit() {
    this.userService.authenticatedGet(ApiUrls.GetModel('inventorycategory'), {}).subscribe((res: any) => {
      this.existingCategories = res.data ;
    });
    this.inventorySevice.inventoryObservable.subscribe(newCategory => {
      this.userService.authenticatedGet(ApiUrls.GetModel('inventorycategory'), {}).subscribe((res: any) => {
        this.existingCategories = res.data ;
        this.existingCategory = this.existingCategories.filter(value => value.name === newCategory.name );
        if (this.existingCategory !== null) {
        this.existingCategories.push(this.existingCategory[0]);
        this.inventorySevice.emitProperties(null);
        }
        this.existingCategory = null;

      });
    });
    this.inventorySevice.inventoryCategoryEditObservable.subscribe(res => {
      const editedCategory = JSON.parse(localStorage.getItem('editedCategory'));
      this.existingCategories.filter(value => value.id === editedCategory.id)
      .map(cat => cat.name = editedCategory.name);
      console.log(this.existingCategories.filter(value => value.id === editedCategory.id));
    });
  }

  toggleEdit() {
this.buttonEditable = !this.buttonEditable;
  }

  onEditSelected() {
console.log(this.list.selectedOptions.selected[0].value);
     this.dialog.open(RegisterInventoryCategoryComponent, {
      width: '450px',
       data: {data: this.list.selectedOptions.selected[0].value}
     });
  }

  onRemoveSelected() {
    console.log(ApiUrls.GetModel('inventorycategory') +
    this.list.selectedOptions.selected[0].value.id.toString());
    if (confirm('Click OK to delete the selected rows')) {
      this.userService.authenticatedDelete(ApiUrls.PatchModel('inventorycategory') +
      this.list.selectedOptions.selected[0].value.id.toString()).subscribe((res) => {
        this.snackBar.open('Deleted !', 'X', {
          duration: 4000,
          panelClass: ['error-snackbar']
        });
        this.existingCategories = this.existingCategories.filter(value => value.id !== this.list.selectedOptions.selected[0].value.id );
      });
    }
  }

  onAddSelected() {
    this.dialog.open(RegisterInventoryCategoryComponent, {
      width: '350px',
     });
  }

  ngOnDestroy() {
    localStorage.removeItem('editedCatgeory');
  }



}
