import { ApiUrls } from './../../services/api.urls';
import { BusinessService } from './../../services/business.service';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpErrorResponse } from '@angular/common/http';
import { NavigationExtras, Router } from '@angular/router';
import {CdkDragDrop, moveItemInArray, transferArrayItem} from '@angular/cdk/drag-drop';
import { MatDialog, MatSnackBar } from '@angular/material';
import { BusinessSearchFilterComponent } from '../business-searchFilter/business-searchFilter.component';
import { Lead } from 'src/app/_models/Lead';
import { BusinessAddLeadComponent } from './../business-addLead/business-addLead.component';
import { StarRatingComponent } from 'ng-starrating';
import { UserService } from 'src/app/services/authentication/user.service';


@Component({
  selector: 'app-business-dashboard',
  templateUrl: './business-dashboard.component.html',
  styleUrls: ['./business-dashboard.component.css']
})
export class BusinessDashboardComponent implements OnInit, OnDestroy {
  Locations: string [];
   arrLeads: Lead [];
  arrLeadsFiltered: any;
  unsortedLeads: any;
  loadQualifiedLeads = false;
  loadDisqualifiedLeads = false;
  loadProspectLeads = false;
  loadNewLeads = false;
  loadCustomerLeads = false;
  // public leadStage: string;
  // public inventoryData: any;
  public inventoryUniqueData: any;
  public inventoryItemData: any;
  public newMapData: any;
  public leadToBeConverted: any;
  public leadStages: any;
  leadFilterData = 'All' ;
  leadFilterLocation = 'All' ;
  property: any;
lists: any = [];

  qualifiedLeadArray = [];
  disqualifiedLeadArray = [];
  prospectLeadArray = [];
  newLeadArray = [];
  customerLeadArray = [];
  customerLeadsArray = [];

  drop(event: any, newArray: any) {
    if (event.previousContainer === event.container) {
      moveItemInArray(event.container.data, event.previousIndex, event.currentIndex);
    // console.log(this.qualifiedLeadArray);
    } else {
      transferArrayItem(event.previousContainer.data,
                        event.container.data,
                        event.previousIndex,
                        event.currentIndex);
                        this.leadToBeConverted = event.container.data.filter
                        (res => res.leadStage !== newArray.leadStage);
                        console.log(newArray.leadStage);
                        console.log(this.leadToBeConverted);
                        this.updateLead(this.leadToBeConverted, newArray.leadStage);

    }
  }

  constructor(private httpService: HttpClient, private router: Router, public dialog: MatDialog,
     private businessService: BusinessService, private userService: UserService, private snackBar: MatSnackBar) { }

  ngOnInit() {
    this.userService.authenticatedGet(ApiUrls.GetModel('leadstage'), {}).subscribe((res: any) => {
      this.leadStages = res.data ;
      console.log(res);
    });
    this.userService.authenticatedGet(ApiUrls.GetModel('leads'), {}).subscribe((res: any) => {
      this.arrLeads = res.data as Lead[];
      console.log(res);
      this.unsortedLeads = res.data;
      this.loadAllLeads();
    });
    this.businessService.propertiesObservable.subscribe(value => {
      this.leadFilterData = value;

      this.changeLeadsData(value);
    });
    this.businessService.locationsObservable.subscribe(value => {
       this.leadFilterLocation = value;
      this.changeLeadsLocation(value);
    });
    this.businessService.leadObservable.subscribe(value => {
      this.newLeadArray.push(value);
    });
    const storedNames = JSON.parse(localStorage.getItem('leadData'));
  }

  ngOnDestroy() {
    localStorage.removeItem('stageFilters');
  }

  loadAllLeads() {
this.updateList();

if (this.leadFilterLocation !== 'All') {
  this.filterFinalByLocation(this.leadFilterLocation);
}
if (this.leadFilterData !== 'All') {
  this.filterFinalByStage(this.leadFilterData, 'One');
}
  }

  updateList() {
    for ( let i = 0; i < this.leadStages.length; i++) {
      this.lists[i] = this.unsortedLeads.filter(lead => lead.leadStage === this.leadStages[i].Name);
   }

  }

  onRate($event: {oldValue: number, newValue: number, starRating: StarRatingComponent}) {
    alert(`Old Value:${$event.oldValue},
      New Value: ${$event.newValue},
      Checked Color: ${$event.starRating.checkedcolor},
      Unchecked Color: ${$event.starRating.uncheckedcolor}`);
  }

  changeLeadsData(selectedStage ) {
    this.arrLeadsFiltered = this.unsortedLeads.filter(lead => lead.leadStage === selectedStage);
    if ( selectedStage === 'All' || selectedStage.length < 1) {
      this.leadFilterData = 'All';
      this.loadAllLeads();
    } else {
      this.filterFinalByStage(selectedStage, 'All');
    }

  }

  changeLeadsLocation(selectedLocation) {
    if (selectedLocation === 'All'  || selectedLocation.length < 1) {

      this.leadFilterLocation = 'All';
      this.loadAllLeads();
    } else {
      if (this.leadFilterData === 'All') {
    this.loadAllLeads();
      } else {
      this.changeLeadsData(this.leadFilterData);
      }
      this.filterFinalByLocation(selectedLocation);
    }
  }

  filterFinalByLocation(selectedLocation) {

    this.qualifiedLeadArray = this.qualifiedLeadArray.filter((word) => selectedLocation.includes(word.Address));
    this.newLeadArray = this.newLeadArray.filter((word) => selectedLocation.includes(word.Address));
    this.prospectLeadArray = this.prospectLeadArray.filter((word) => selectedLocation.includes(word.Address));
    this.customerLeadArray = this.customerLeadArray.filter((word) => selectedLocation.includes(word.Address));
    this.disqualifiedLeadArray = this.disqualifiedLeadArray.filter((word) => selectedLocation.includes(word.Address));
    this.updateList();
  }

  filterFinalByStage(selectedStage, filterAll) {
    if ( filterAll === 'All') {
      this.loadAllLeads();
    }
    // console.log(this.lists);
    for ( let i = 0; i < this.lists.length; i++) {
      this.lists[i] = this.lists[i].filter((word) => selectedStage.includes(word.leadStage));
     // console.log(this.lists[i]);
   }
   
  }

  updateLead(form: any, newLeadStage: string): void {
    const submissionForm = {
      name: form[0].name,
      address: form[0].address,
      leadStage: newLeadStage,
      organisation: form[0].organisation
    };
    this.userService.authenticatedPatch(ApiUrls.PatchModel('leads').toString() + form[0].id,
          JSON.stringify(submissionForm), {}).subscribe(
            res => {
              this.snackBar.open('Lead successfully updated !', 'X', {
                duration: 4000,
                panelClass: ['success-snackbar'],
                verticalPosition: 'top'
              });
            },
            err => {
              console.log('Error occured');
              console.log(err);
              this.snackBar.open('Lead updation failed !', 'X', {
                duration: 4000,
                panelClass: ['error-snackbar'],
                verticalPosition: 'top'
              });
            }
          );
  }

  editLeadDetails(property: any) {
    // storing common data using shared service
    this.businessService.leadData = property;

    // storing common data using local storage
    localStorage.setItem('leadData', JSON.stringify(property));

    this.router.navigate(['/viewBusinessLeads']);
  }

  onSearch() {
    this.dialog.open(BusinessSearchFilterComponent, {
      width: '450px',
     });
  }

  addLead() {
    this.dialog.open(BusinessAddLeadComponent, {
      width: '350px',
     });
  }

}
