import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MatDialog, MatSnackBar } from '@angular/material';
import { AddStepComponent } from '../addStep/addStep.component';
import { UserService } from 'src/app/services/authentication/user.service';
import { ApiUrls } from '../../services/api.urls';

@Component({
  selector: 'app-addProcess',
  templateUrl: './addProcess.component.html',
  styleUrls: ['./addProcess.component.css']
})
export class AddProcessComponent implements OnInit {
  public registrationForm: FormGroup;

  constructor(public dialogRef: MatDialogRef<AddProcessComponent>, public fb: FormBuilder,
    public userService: UserService, public dialog: MatDialog, private snackBar: MatSnackBar) {
      this.registrationForm = fb.group({
        'Process' : [null, Validators.required]
     });
    }

  ngOnInit() {

  }

  registerInventoryItem(value: any, form): void {
    console.log(this.registrationForm.value);
    this.userService.authenticatedPost(ApiUrls.GetModel('leads'), JSON.stringify(this.registrationForm.value), {}).subscribe(
     res => {
       this.snackBar.open('Lead Added !', 'X', {
         duration: 4000,
         panelClass: ['success-snackbar'],
         verticalPosition: 'top'
       });
       this.registrationForm.reset();
     },
     err => {
       console.log("Error occured");
       console.log(err);
       this.snackBar.open('Lead creation failed !', 'X', {
         duration: 4000,
         panelClass: ['error-snackbar'],
         verticalPosition: 'top'
       });
     }
   );
   }


  addStep() {
    this.dialog.open(AddStepComponent, {
      width: '550px',
     });
  }




  closeDialog() {
    this.dialogRef.close();
  }

}
