import { RegisterInventoryCategoryComponent } from './inventory/RegisterInventoryCategory/RegisterInventoryCategory.component';
import { InventoryCategoryComponent } from './inventory/Inventory-Category/Inventory-Category.component';
import { ViewProcessComponent } from './manufacturing/viewProcess/viewProcess.component';
import { ReviewProductionReportComponent } from './manufacturing/reviewProductionReport/reviewProductionReport.component';
import { DefineProductionRunComponent } from './manufacturing/defineProductionRun/defineProductionRun.component';
import { CreateProductionRunComponent } from './manufacturing/createProductionRun/createProductionRun.component';
import { ProcessComponent } from './manufacturing/process/process.component';
import { EditQuantityComponent } from './manufacturing/editQuantity/editQuantity.component';
import { AddRawMaterialComponent } from './manufacturing/addRawMaterial/addRawMaterial.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RegisterComponent } from './inventory/Register/Register.component';
import { RegisterInventoryComponent } from './inventory/RegisterInventory/RegisterInventory.component';

import { FullComponent } from './layouts/full/full.component';
import { BlankComponent } from './layouts/blank/blank.component';
import {
  NonLoggedInGuard,
  LoggedInGuard
} from './services/authentication/login.guard';
import { NotfoundComponent } from './404/not-found.component';
import { InventoryItemDisplayComponent } from './inventory/InventoryItem-display/InventoryItem-display.component';
import { InventoryDisplayComponent } from './inventory/Inventory-Display/Inventory-Display.component';
import { InventoryDashboardComponent } from './inventory/Inventory-Dashboard/Inventory-Dashboard.component';
import { BusinessSettingsComponent } from './business/business-settings/business-settings.component';
import { BusinessLeadDetailsComponent } from './business/business-leadDetails/business-leadDetails.component';
import { BusinessDashboardComponent } from './business/business-dashboard/business-dashboard.component';
import { ProductionDashboardComponent } from './manufacturing/productionDashboard/productionDashboard.component';
import { ReviewProductionFinalComponent } from './manufacturing/reviewProductionFinal/reviewProductionFinal.component';
import { SendForProductionComponent } from './manufacturing/sendForProduction/sendForProduction.component';
import { CreateProductDesignComponent } from './manufacturing/createProductDesign/createProductDesign.component';
import { EnterComponentsQuantityComponent } from './manufacturing/enterComponentsQuantity/enterComponentsQuantity.component';

export const Approutes: Routes = [
  {
    path: '',
    children: [
      {
        path: '',
        component: BlankComponent,
        children: [
          { path: '', redirectTo: 'authentication', pathMatch: 'full' },
          {
            path: 'authentication',
            loadChildren:
              './authentication/authentication.module#AuthenticationModule'
          }
        ],
        canActivate: [NonLoggedInGuard]
      },
      {
        path: '',
        component: FullComponent,
        children: [
          // { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
          {
            path: 'dashboard',
            loadChildren: './dashboards/dashboard.module#DashboardModule'
          },
          {
            path: 'component',
            loadChildren: './component/component.module#ComponentsModule'
          },
          {
            path: 'manage',
            loadChildren: './manage/application.module#ApplicationModule'
          },
          { path: 'maps', loadChildren: './maps/maps.module#MapsModule' },
          {
            path: 'sample-pages',
            loadChildren: './sample-pages/sample-pages.module#SamplePagesModule'
          },
          {
            path: 'viewInventoryDashboard',
            component: InventoryDashboardComponent
          },
          {
            path: 'viewInventoryItem',
            component: InventoryItemDisplayComponent
          },
          {
            path: 'viewInventory',
            component: InventoryDisplayComponent
          },
          {
            path: 'registerInventoryCategory',
            component: RegisterInventoryCategoryComponent
          },
          {
            path: 'viewInventoryCategory',
            component: InventoryCategoryComponent
          },
          {
            path: 'viewBusinessDashboard',
            component: BusinessDashboardComponent
          },
          {
            path: 'viewBusinessLeads',
            component: BusinessLeadDetailsComponent
          },
          {
            path: 'viewBusinessSettings',
            component: BusinessSettingsComponent
          },
          {
            path: 'process',
            component: ProcessComponent
          },
          {
            path: 'viewProcess',
            component: ViewProcessComponent
          },
          {
            path: 'createProductDesign',
            component: CreateProductDesignComponent
          },
          {
            path: 'createProductionRun',
            component: CreateProductionRunComponent
          },
          {
            path: 'defineProductionRun',
            component: DefineProductionRunComponent
          },
          {
            path: 'reviewProductionRun',
            component: ReviewProductionFinalComponent
          },
          {
            path: 'reviewProduction',
            component: ReviewProductionReportComponent
          },
          {
            path: 'sendForProduction',
            component: SendForProductionComponent
          },
          {
            path: 'productionDashboard',
            component: ProductionDashboardComponent
          },
          {
            path: 'addRawMaterial',
            component: AddRawMaterialComponent
          },
          {
            path: 'editQuantity',
            component: EditQuantityComponent
          },
          {
            path: 'enterComponentsQuantity',
            component: EnterComponentsQuantityComponent
          }
        ],
        canActivate: [LoggedInGuard]
      },
      {
        path: '**',
        component: NotfoundComponent
      }
    ]
  }
];
