import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormArray } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-reviewProductionFinal',
  templateUrl: './reviewProductionFinal.component.html',
  styleUrls: ['./reviewProductionFinal.component.css']
})
export class ReviewProductionFinalComponent implements OnInit {
  form: FormGroup;
  productForm: FormGroup;
  wastageForm: FormGroup;
  productDetails: any;
  stepForm: any;
  additionalProductForm: FormGroup;
  additionalProductDetails: any;
  itemForm: any;
  productData: any;

  constructor(private fb: FormBuilder, private fbProduct: FormBuilder,
    private fbAdditionalProduct: FormBuilder, private fbWastage: FormBuilder, private router: Router) { }

  ngOnInit() {
    this.productData = JSON.parse(localStorage.getItem('productionForm'));
    this.itemForm = JSON.parse(localStorage.getItem('itemForm'));
    this.productDetails = JSON.parse(localStorage.getItem('productForm'));
    console.log(this.productForm);
    this.stepForm = JSON.parse(localStorage.getItem('stepForm'));
    this.additionalProductDetails = JSON.parse(localStorage.getItem('additionalProductForm'));
    this.form = this.fb.group({
      items: this.fb.array([])
    });
    this.productForm = this.fbProduct.group({
      mainProducts: this.fbProduct.array([])
    });
    this.additionalProductForm = this.fbAdditionalProduct.group({
      additionalProducts: this.fbAdditionalProduct.array([])
    });

    this.wastageForm = this.fbWastage.group({
      wasteItems: this.fbWastage.array([this.createItem()])
    });

    for (let i = 0; i < this.itemForm.newItems.length; i++) {
      this.addExisting(this.itemForm.newItems[i].item);
    }

    for (let j = 0; j < this.productDetails.mainProducts.length; j++) {
      this.addNextProduct(this.productDetails.mainProducts[j].product);
    }

    for (let k = 0; k < this.additionalProductDetails.additionalProducts.length; k++) {
      this.addNextAdditionalProduct(this.additionalProductDetails.additionalProducts[k].additionalProduct);
    }
  }



  addExistingItem(newItem) {
    return this.fb.group({
      item: [newItem],
      quantity: []
    });
  }

  addExisting(newItem) {
    (this.form.controls['items'] as FormArray).push(this.addExistingItem(newItem));
  }

  addNextProduct(newProduct) {
    (this.productForm.controls['mainProducts'] as FormArray).push(this.createProduct(newProduct));
  }


  deleteRowProduct(index: number) {
    (this.productForm.controls['mainProducts'] as FormArray).removeAt(index);
  }

  createAdditionalProduct(additionalProduct) {
    return this.fbAdditionalProduct.group({
      additionalProduct: [additionalProduct],
      quantity: []
    });
  }

  addNextAdditionalProduct(additionalProduct) {
    (this.additionalProductForm.controls['additionalProducts'] as FormArray).push(this.createAdditionalProduct(additionalProduct));
  }


  deleteRowAdditionalProduct(index: number) {
    (this.additionalProductForm.controls['additionalProducts'] as FormArray).removeAt(index);
  }

  createProduct(newProduct) {
    return this.fbProduct.group({
      product: [newProduct],
      quantity: []
    });
  }

  createItem() {
    return this.fbWastage.group({
      name: [''],
      quantity: ['']
    });
  }

  addNext() {
    (this.wastageForm.controls['wasteItems'] as FormArray).push(this.createItem());
  }

  deleteRow(index: number) {
    (this.wastageForm.controls['wasteItems'] as FormArray).removeAt(index);
  }

  save() {
    try {

      localStorage.removeItem('itemForm');
      localStorage.removeItem('productForm');
      localStorage.removeItem('additionalProductForm');
      } catch (e) {
        console.log(e);
      }
//console.log(this.itemForm.value);
console.log(this.form.value);
      localStorage.setItem('itemForm', JSON.stringify(this.form.value));
      localStorage.setItem('productForm', JSON.stringify(this.productForm.value));
      localStorage.setItem('additionalProductForm', JSON.stringify(this.additionalProductForm.value));
    localStorage.setItem('wastageForm', JSON.stringify(this.wastageForm.value));
    this.router.navigate(['/sendForProduction']);
  }




}
