import { Component, OnInit, ViewChild } from '@angular/core';
import { GridOptions } from 'ag-grid-community';
import { UserService } from 'src/app/services/authentication/user.service';
import { MatSnackBar, MatDialog } from '@angular/material';
import { InventoryEditComponent } from '../Inventory-Edit/Inventory-Edit.component';
import { BaseChartDirective } from 'ng2-charts';
import { DatePipe } from '@angular/common';
import { ApiUrls } from 'src/app/services/api.urls';

@Component({
  selector: 'app-Inventory-Dashboard',
  templateUrl: './Inventory-Dashboard.component.html',
  styleUrls: ['./Inventory-Dashboard.component.css']
})
export class InventoryDashboardComponent implements OnInit {
  @ViewChild(BaseChartDirective) chart: BaseChartDirective;
  public rowData: any;
  public inventory: string;
  public inventoryData: any;
  public inventoryUniqueData: any;
  public inventoryItemData: any;
  public newMapData: any;
  public rowSelection;
  public defaultColDef;
  public overlayNoRowsTemplate;
  gridOptions: GridOptions = { };
  public activateDelBtn = false;
  public activateEditBtn = false;
  public newChartData: number[] = [];
  public newChartLabel: string[] = [];

  public barChartOptions = {
    scaleShowVerticalLines: false,
    responsive: true
  };

  public barChartLabels = ['2006', '2007', '2008', '2009', '2010', '2011', '2012'];
  public barChartType = 'bar';
  public barChartLegend = true;
  public barChartData = [
    {data: [65, 59, 80, 81, 56, 55, 40], label: 'Series A'},
    {data: [28, 48, 40, 19, 86, 27, 90], label: 'Series B'},
    {data: this.newChartData, label: 'Product'},
  ];
  public doughnutChartLabels = ['Sales Q1', 'Sales Q2', 'Sales Q3', 'Sales Q4'];
  public doughnutChartData = this.newChartData;
  public doughnutChartType = 'doughnut';
  public pieChartLabels = ['Sales Q1', 'Sales Q2', 'Sales Q3', 'Sales Q4'];
  public pieChartData = this.newChartData;
  public pieChartType = 'pie';

    columnDefs = [
    {headerName: 'Name', field: 'name', sortable: true, filter: true },
    {headerName: 'ItemId', field: 'itemId', sortable: true, filter: true },
    {headerName: 'Id', field: 'id', sortable: true, filter: true },
    {headerName: 'Quantity Amount', field: 'quantityAmount', sortable: true, filter: true},
    {headerName: 'Quantity Unit', field: 'quantityUnit', sortable: true, filter: true},
  ];

  constructor(public userService: UserService,  private snackBar: MatSnackBar, public dialog: MatDialog,
    private datePipe: DatePipe) {

     this.rowSelection = 'single';
     this.defaultColDef = { resizable: true };

     this.overlayNoRowsTemplate =
     '<span class="ag-overlay-loading-center">Please wait while your rows are loading</span>';
   }

   onEditSelected() {
    const selectedRows = this.gridOptions.api.getSelectedRows();
      this.dialog.open(InventoryEditComponent, {
       width: '450px',
        data: {data: selectedRows[0], activateEditBtn: true}
      });
  }



   getInventoryName(inventoryId) {
    return  this.inventoryItemData.filter(inventoryData => inventoryData.id === inventoryId)[0].name;

  }

  loadGrid() {
    this.userService.authenticatedGet(ApiUrls.GetModel('inventory'), {}).subscribe((res: any) => {
      this.rowData = res.data;
      console.log(this.rowData);
      this.finalCombine();
    });

  }

  finalCombine() {
    this.userService.authenticatedGet(ApiUrls.GetModel('inventoryitem'), {}).subscribe((res: any) => {
    this.inventoryItemData = res.data;
    // adds the name field to inventory data
      this.inventoryData = this.rowData.map( inventory => {
        return {id: inventory.id, itemId: inventory.itemId, quantityAmount: inventory.quantityAmount ,
          quantityUnit: inventory.quantityUnit, name: this.getInventoryName(inventory.itemId),
           time: this.datePipe.transform(inventory.time, 'dd-MM-yyyy')  };
    });
    console.log(this.inventoryData);
    for ( let i = 0; i < this.inventoryData.length; i++) {
      this.newChartData.push(this.inventoryData[i].quantityAmount);
      this.newChartLabel.push(this.inventoryData[i].name);
   }
   // finding unique values in array
   this.inventoryUniqueData = this.inventoryData
                 .map(item => item.name)
                 .filter((value, index, self) => self.indexOf(value) === index);
                 console.log(this.inventoryUniqueData);
  // below part fills the chart data
   this.barChartLabels = this.newChartLabel;
   this.doughnutChartLabels = this.newChartLabel;
   this.pieChartLabels = this.newChartLabel;
   var max = Math.max(...this.newChartData);
   for ( let i = 0; i < this.newChartData.length; i++) {
     this.newChartData[i] = this.newChartData[i] / max * 100;
  }
  this.doughnutChartData = this.newChartData;
  this.chart.chart.update();
    this.gridOptions.api.setRowData(this.inventoryData);
    });

  }

  onSelectionChanged(event) {
    const selectedRows = this.gridOptions.api.getSelectedRows();

    if (selectedRows.length > 0) {
      this.activateDelBtn = true;
    } else {
      this.activateDelBtn = false;
    }
    if (selectedRows.length === 1) {
      this.activateEditBtn = true;

    } else {
      this.activateEditBtn = false;

    }
  }

  ngOnInit() {
    this.loadGrid();

  }

  ngAfterViewInit() {
    this.gridOptions.api.setColumnDefs([
    {headerName: 'Name', field: 'name', sortable: true, filter: true },
    {headerName: 'Quantity Amount', field: 'quantityAmount', sortable: true, filter: true},
    {headerName: 'Quantity Unit', field: 'quantityUnit', sortable: true, filter: true},
    ]);

    this.gridOptions.api.sizeColumnsToFit();
  }

  changeMapsData(inventoryName) {
    this.newMapData = this.inventoryData.filter(inventoryData => inventoryData.name === inventoryName);
    this.newChartData.length = 0;
    this.newChartData = [];
    this.newChartLabel.length = 0;
    this.doughnutChartData.length = 0;
    this.doughnutChartLabels.length = 0;
    this.doughnutChartLabels = [];
    this.pieChartData.length = 0;
    this.pieChartLabels.length = 0;
    this.pieChartLabels = [];
    for ( let i = 0; i < this.newMapData.length; i++) {
      this.newChartData.push(this.newMapData[i].quantityAmount);
      this.newChartLabel.push(this.newMapData[i].time);
   }
   //this.doughnutChartLabels = this.newChartLabel;
   this.doughnutChartLabels.push(...this.newChartLabel);
   this.doughnutChartData = this.newChartData;
   this.pieChartLabels.push(...this.newChartLabel);
   this.pieChartData = this.newChartData;
   this.chart.chart.update();
    console.log(this.doughnutChartLabels);
  }





}
