import { MatSnackBar } from '@angular/material';
import { UserService } from './../../services/authentication/user.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ApiUrls } from 'src/app/services/api.urls';

@Component({
  selector: 'app-viewProcess',
  templateUrl: './viewProcess.component.html',
  styleUrls: ['./viewProcess.component.css']
})
export class ViewProcessComponent implements OnInit {
  registrationForm: FormGroup;
  selectedProcess: any;

  constructor(public fb: FormBuilder, private userService: UserService, private snackBar: MatSnackBar) {
    this.registrationForm = fb.group({
      'name' : [null, Validators.required],
      'product' : [null, Validators.required],
      'productId' : [null, Validators.required],
      'steps' : [null, Validators.required]
   });
   }

  ngOnInit() {
    this.selectedProcess = JSON.parse(localStorage.getItem('selectedProcess'));
    console.log(this.selectedProcess);
    this.registrationForm.patchValue({
      'name': this.selectedProcess.name,
      'product': this.selectedProcess.product
    });
  }

  saveChanges() {
    console.log(this.registrationForm.value);
    this.userService.authenticatedPatch(ApiUrls.GetModel('process'),
        JSON.stringify(this.registrationForm.value), {}).subscribe(
          res => {
            this.snackBar.open('Process successfully updated !', 'X', {
              duration: 4000,
              panelClass: ['success-snackbar'],
              verticalPosition: 'top'
            });
            this.registrationForm.reset();
          },
          err => {
            console.log('Error occured');
            console.log(err);
            this.snackBar.open('Process updation failed !', 'X', {
              duration: 4000,
              panelClass: ['error-snackbar'],
              verticalPosition: 'top'
            });
          }
        );
  }

}
