import { map } from 'rxjs/operators';
import { RegisterComponent } from './../../inventory/Register/Register.component';
import { MatSnackBar } from '@angular/material';
import { UserService } from './../../services/authentication/user.service';
import { ApiUrls } from './../../services/api.urls';
import { BusinessService } from './../../services/business.service';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { LeadActivity } from './../../_models/LeadActivity';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'app-business-leadDetails',
  templateUrl: './business-leadDetails.component.html',
  styleUrls: ['./business-leadDetails.component.css']
})
export class BusinessLeadDetailsComponent implements OnInit, OnDestroy {
  public registrationForm: FormGroup;
  newActivityForm: FormGroup;
  public httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };
  arrActivities: LeadActivity[];
  private sub: any;
  private selectedLead: any;
  public storedNames: any;
  public editLeadDetails = false;
  public presentLeadStage;
  public leadActivities: Array<string> = ['Call', 'Meeting', 'Note'];
  public leadStages: Array<string> = ['New', 'Qualified', 'Prospect', 'Customer', 'Disqualified'];

  constructor(private router: Router, private route: ActivatedRoute, private businessService: BusinessService,
    public fb: FormBuilder, private httpService: HttpClient, private userService: UserService, private snackBar: MatSnackBar) {
      this.storedNames = JSON.parse(localStorage.getItem('leadData'));
      console.log(this.storedNames);
      this.registrationForm = fb.group({
        'name' : [this.storedNames.name, Validators.required],
        'address' : [this.storedNames.address, Validators.required],
        'id' : [this.storedNames.id, Validators.required],
        'leadStage' : [this.storedNames.leadStage, Validators.required],
        'organisation' : [this.storedNames.organisation, Validators.required],
     });
     this.newActivityForm = fb.group({
      'activity' : [null, Validators.required],
      'activityType' : [null, Validators.required],
   });

this.registrationForm.patchValue({
  leadStage: this.storedNames.leadStage
});
   console.log(this.storedNames);
    }

  ngOnInit() {
    this.selectedLead = this.businessService.leadData;
    this.storedNames = JSON.parse(localStorage.getItem('leadData'));
    this.presentLeadStage = this.storedNames.leadStage;
    this.registrationForm.patchValue({
      leadStage: this.storedNames.leadStage
    });
    this.userService.authenticatedGet(ApiUrls.GetActivity(this.storedNames.id) , {}).subscribe((res: any) => {
      this.arrActivities = res.data;
    });
  }

ngOnDestroy() {
  localStorage.removeItem('leadData');
}

activateEditLeadDetails() {
  this.editLeadDetails = !this.editLeadDetails;
}

saveEditedLeadDetails() {
  console.log(this.registrationForm);
}

updateLead(value: any, form): void {
  const submissionForm = {
    name: this.registrationForm.value.name,
    address: this.registrationForm.value.address,
    leadStage: this.registrationForm.value.leadStage,
    organisation: this.registrationForm.value.organisation
  };
  this.userService.authenticatedPatch(ApiUrls.PatchModel('leads').toString() + this.storedNames.id,
        JSON.stringify(submissionForm), {}).subscribe(
          res => {
            this.snackBar.open('Lead successfully updated !', 'X', {
              duration: 4000,
              panelClass: ['success-snackbar'],
              verticalPosition: 'top'
            });
            this.editLeadDetails = !this.editLeadDetails;
          },
          err => {
            console.log('Error occured');
            console.log(err);
            this.snackBar.open('Lead updation failed !', 'X', {
              duration: 4000,
              panelClass: ['error-snackbar'],
              verticalPosition: 'top'
            });
          }
        );
}

registerActivity(): void {

 const formvalue = {
  activity: this.newActivityForm.value.activity,
  activityType: this.newActivityForm.value.activityType,
  leadTime: Date.now(),
  userId: this.storedNames.id
 };
 this.userService.authenticatedPost(ApiUrls.GetModel('leadsactivity'), JSON.stringify(formvalue), {}).subscribe(
  res => {
    this.snackBar.open('Lead Added !', 'X', {
      duration: 4000,
      panelClass: ['success-snackbar'],
      verticalPosition: 'top'
    });
    const formPushvalue = {
      activity: this.newActivityForm.value.activity,
      activityType: this.newActivityForm.value.activityType,
      leadTime: Date.now().toString(),
      userId: this.storedNames.id,
      id: '',
      time: Date.now().toString()
     };
    this.arrActivities.push(formPushvalue);
    //  this.registrationForm.reset();
    //  this.dialogRef.close();
  },
  err => {
    console.log('Error occured');
    console.log(err);
    this.snackBar.open('Lead creation failed !', 'X', {
      duration: 4000,
      panelClass: ['error-snackbar'],
      verticalPosition: 'top'
    });
  }
);
}

previewImage(value) {
console.log(value);
}

}
