import { UserService } from 'src/app/services/authentication/user.service';
import { ProductionModel } from './../../_models/Production';
import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material';
import { BusinessService } from 'src/app/services/business.service';
import {CdkDragDrop, moveItemInArray, transferArrayItem} from '@angular/cdk/drag-drop';
import { ApiUrls } from 'src/app/services/api.urls';

@Component({
  selector: 'app-productionDashboard',
  templateUrl: './productionDashboard.component.html',
  styleUrls: ['./productionDashboard.component.css']
})
export class ProductionDashboardComponent implements OnInit {
  arrProduction: ProductionModel [];
  unsortedLeads: any;
  toBeDoneProduction: ProductionModel [];
  ongoingProduction: ProductionModel [];
  completedProduction: ProductionModel [];
  lists: any = [];
  rowData: any;

  constructor(private httpService: HttpClient, private router: Router, public dialog: MatDialog,
    private businessService: BusinessService, private userService: UserService) { }

  ngOnInit() {
  this.userService.authenticatedGet(ApiUrls.GetModel('productionmodel'), {}).subscribe((data: any) => {
    this.arrProduction = data.data as ProductionModel[];
    console.log(this.arrProduction);
    this.unsortedLeads = data.data;
    this.loadAllData();
   });
 }


 loadAllData() {
  this.toBeDoneProduction = this.arrProduction.filter(production => production.stage === 'toBeDone');
  this.ongoingProduction = this.arrProduction.filter(production => production.stage === 'ongoing');
  this.completedProduction = this.arrProduction.filter(production => production.stage === 'completed');
  this.lists[0] = this.toBeDoneProduction;
  this.lists[1] = this.ongoingProduction;
  this.lists[2] = this.completedProduction;
 }

 drop(event: any, leadStage: string) {
  if (event.previousContainer === event.container) {
    moveItemInArray(event.container.data, event.previousIndex, event.currentIndex);
    console.log(leadStage);
  } else {
    transferArrayItem(event.previousContainer.data,
                      event.container.data,
                      event.previousIndex,
                      event.currentIndex);
                      console.log(leadStage);
                      console.log(event);
  }


}

addProduction() {
  this.router.navigate(['/createProductionRun']);
}

editProductionDetails(property: any) {
  // storing common data using shared service
  // this.businessService.leadData = property;

  // // storing common data using local storage
  // localStorage.setItem('leadData', JSON.stringify(property));

  // this.router.navigate(['/viewBusinessLeads']);
  localStorage.setItem('editProduction', JSON.stringify(property));
  this.router.navigate(['/createProductionRun']);

  // console.log(property);
}

}
