import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class BusinessService {
  public leadData: any;
  public propertiesObservable = new Subject<any>();
  public locationsObservable = new Subject<any>();
  public leadObservable = new Subject<any>();
  public Locations: Array<string> = [];
  public Stages: Array<string> = [];

constructor() { }

emitProperties(val) {
  this.Stages.push(val);
  this.propertiesObservable.next(this.Stages);
  localStorage.setItem('stageFilters', JSON.stringify(this.Stages));
}

emitLead(lead) {
  this.leadObservable.next(lead);
}

emitLocations(val) {
  this.Locations.push(val);
  this.locationsObservable.next(this.Locations);
}

addLocationToSearch(newLocation: any) {
  this.Locations.push(newLocation.value);
}

giveLocationsToSearch()  {
    return Observable.of(this.Locations);
}

removeLocation(val) {
  this.Locations = val;
  this.locationsObservable.next(this.Locations);
}

removeStage(val) {
  this.Stages = val;
  this.propertiesObservable.next(this.Stages);
}

}
