import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";

import { NgbModule } from "@ng-bootstrap/ng-bootstrap";
import { UserService } from "./authentication/user.service";

import { MatDialogModule, MatSnackBarModule } from "@angular/material";
import { LoadingUiService } from "./loading-ui.service";
import { ApiUrls } from "./api.urls";
import { LoggedInGuard, NonLoggedInGuard } from "./authentication/login.guard";
import { ModuleWithProviders } from "@angular/compiler/src/core";
import { MemCacheService } from "./cache/mem-cache.service";

@NgModule({
  imports: [
    CommonModule,
    NgbModule,
    FormsModule,
    ReactiveFormsModule,
    MatDialogModule,
    MatSnackBarModule
  ],
  declarations: []
  // providers: [UserService, LoadingUiService, {provide: 'ApiUrls', useValue: ApiUrls}, LoggedInGuard, NonLoggedInGuard]
})
export class SerivcesModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SerivcesModule,
      providers: [
        UserService,
        MemCacheService,
        LoadingUiService,
        { provide: "ApiUrls", useValue: ApiUrls },
        LoggedInGuard,
        NonLoggedInGuard
      ]
    };
  }
}
