import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MatSnackBar, MatDialog } from '@angular/material';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { UserService } from 'src/app/services/authentication/user.service';
import { ApiUrls } from 'src/app/services/api.urls';

@Component({
  selector: 'app-AddSubAssemblyComponent',
  templateUrl: './AddSubAssemblyComponent.component.html',
  styleUrls: ['./AddSubAssemblyComponent.component.css']
})
export class AddSubAssemblyComponentsComponent implements OnInit {
  newAssemblyName: string;
  assemblyName: string;
  newSubAssemblyName: string;
  form: FormGroup;
  newData: any;

  constructor(public fb: FormBuilder, public dialogRef: MatDialogRef<AddSubAssemblyComponentsComponent>,
    private userService: UserService, private snackBar: MatSnackBar, public fbItem: FormBuilder,
    private dialog: MatDialog) {
    this.form = fb.group({
      'name' : [null, Validators.required],
      'minQuantity' : [null, Validators.required],
      'scrapFactor' : [null, Validators.required]
   });
  }

  ngOnInit() {
this.newSubAssemblyName = localStorage.getItem('subAssemblyName');
this.newAssemblyName = localStorage.getItem('assemblyName');
this.form = this.fb.group({
  components: this.fb.array([this.createItem()])
});
  }


   deleteRow(index: number) {
    (this.form.controls['components'] as FormArray).removeAt(index);
  }

  addNext() {
    (this.form.controls['components'] as FormArray).push(this.createItem());
  }

  createItem() {
    return this.fbItem.group({
      name: [''],
      minQuantity: [''],
      scrapFactor: ['']
    });
  }

  closeDialog() {
    this.dialogRef.close();
  }

  save() {
    this.newData = this.form.value.components.map( component => {
      return {name: component.name, minQuantity: component.minQuantity, 
        scrapFactor: component.scrapFactor, subAssemblyId: this.newSubAssemblyName,
         assemblyId: this.newAssemblyName  };
  });
  console.log(this.newData);
  }



}
