import { Component, OnInit } from '@angular/core';
import { store } from '@angular/core/src/render3';

@Component({
  selector: 'app-editQuantity',
  templateUrl: './editQuantity.component.html',
  styleUrls: ['./editQuantity.component.css']
})
export class EditQuantityComponent implements OnInit {
  rawMaterials = [];
  rawMaterialsScrapFactor = [];
  rawMaterialsQuantity = [];
  submissionForm: any;
  submissionInputForm: any;
  submissionOutputForm: any;

  constructor() { }

  ngOnInit() {
    var storedItem = JSON.parse(localStorage.getItem('itemData')).items;
    this.submissionOutputForm = JSON.parse(localStorage.getItem('outputData')).outputItems;
    var storedItems = Object.keys(storedItem);
    //console.log(storedItems);

    for (var i = 0, len = storedItem.length; i < len; ++i) {
      var rawMaterials: any = storedItem[i];
      this.rawMaterials.push(rawMaterials.name);
      this.rawMaterialsScrapFactor.push(rawMaterials.scrapFactor);
  }
}



saveAll() {
  var count = 0;
  this.submissionInputForm = this.rawMaterials.map( rawMaterialName => {
    return {name: rawMaterialName, scrapFactor: this.rawMaterialsScrapFactor[count],
    quantity: this.rawMaterialsQuantity[count++]};
});

this.submissionForm =  { consumes: JSON.stringify(this.submissionInputForm),
   output: JSON.stringify(this.submissionOutputForm)};
 
}



}
