import { Component, OnInit, Inject, Output, EventEmitter, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';

import { UserService, AppHttpHandledError, AppServerResponse } from '../../../services/authentication/user.service';
import { ApiUrls } from 'src/app/services/api.urls';

//import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
//import {MatSnackBar} from '@angular/material';

/*
export interface DialogData {
  model: string;
}
*/

@Component({
  selector: 'app-filters',
  templateUrl: './filters.component.html',
  styleUrls: ['./filters.component.css']
})
export class FiltersComponent implements OnInit {
  form: FormGroup;

  @Input('clear') set _clear(val) {
    console.log('Cleared');
    this.form.reset();
  }
  @Output() filterChange = new EventEmitter<any>();

  constructor(public apiUrl: ApiUrls,
    public formBuilder: FormBuilder,
    public userService: UserService,
    ) {
    this.form = this.formBuilder.group({
      'stateId': [null],
      'stateDistrictId': [null],
      'stateConstituencyId': [null],
      'stateMandalId': [null],
      'stateBoothId': [null],
    });
  }

  ngOnInit() {
    this.form.valueChanges.debounceTime(300)
    .distinctUntilChanged()
    .subscribe((res) => {
      const filtered = Object.keys(this.form.value).filter(a => this.form.value[a] != null && this.form.value[a] !== '').map(a => {
        return {[a] : this.form.value[a]};
      });
        this.filterChange.emit((filtered.length > 1) ? filtered.reduce((a, b) => {
          return {...b, ...a};
        }) : (filtered.length > 1) ? filtered[0] : {});
    });
  }
}
