import { InventoryService } from './../../services/inventory.service';
import { UserService } from 'src/app/services/authentication/user.service';
import { Component, OnInit } from '@angular/core';
import { AgTempelateRendererComponent } from '../../views/utils/ag-tempelate-renderer/ag-tempelate-renderer.component';
import { InventoryItemEditComponent } from '../InventoryItem-edit/InventoryItem-edit.component';
import { MatDialog, MAT_DIALOG_DATA, MatSnackBar } from '@angular/material';
import { GridOptions } from 'ag-grid-community';
import { RegisterComponent } from '../Register/Register.component';
import { ApiUrls } from './../../services/api.urls';
import * as moment from 'moment';

@Component({
  selector: 'app-InventoryItem-display',
  templateUrl: './InventoryItem-display.component.html',
  styleUrls: ['./InventoryItem-display.component.css']
})
export class InventoryItemDisplayComponent implements OnInit {
rowData: any;
public rowSelection;
public defaultColDef;
public overlayNoRowsTemplate;
gridOptions: GridOptions = { };
public activateDelBtn = false;
public activateEditBtn = false;

  columnDefs = [
    {headerName: 'Barcode', field: 'barcode', sortable: true, filter: true},
    {headerName: 'Category', field: 'category', sortable: true, filter: true},
    {headerName: 'Type Of Dimension', field: 'dimensionType', sortable: true, filter: true},
    {headerName: 'ID', field: 'id', sortable: true, filter: true},
    {headerName: 'Model', field: 'model', sortable: true, filter: true},
    {headerName: 'Name', field: 'name', sortable: true, filter: true, },
    {headerName: 'Saleable', field: 'saleable', sortable: true, filter: true},
    {headerName: 'Date Of Creation', field: 'time', sortable: true, filter: true}
];




  constructor(public userService: UserService,  private snackBar: MatSnackBar, public dialog: MatDialog, 
    private inventoryService: InventoryService) {

     
     this.rowSelection = 'single';
     this.defaultColDef = { resizable: true };
     this.overlayNoRowsTemplate =
     '<span class="ag-overlay-loading-center">Please wait while your rows are loading</span>';


  }
  onEditSelected() {
    const selectedRows = this.gridOptions.api.getSelectedRows();
    console.log(selectedRows);
     this.dialog.open(InventoryItemEditComponent, {
      width: '450px',
       data: {data: selectedRows[0], activateEditBtn: true}
     });
  }

  onRemoveSelected() {

    const selectedData = this.gridOptions.api.getSelectedRows();
    console.log(selectedData);
    if (confirm('Click OK to delete the selected rows')) {
      this.userService.authenticatedDelete(ApiUrls.PatchModel('inventoryitem') + selectedData[0].id.toString()).subscribe((res) => {
        this.snackBar.open('Deleted !', 'X', {
          duration: 4000,
          panelClass: ['error-snackbar']
        });
        this.gridOptions.api.updateRowData({ remove: selectedData });
        this.rowData.filter(row => row.id !== selectedData[0].id);
      });
    }
  }

  onAddSelected() {
    this.dialog.open(RegisterComponent, {
      width: '350px',
     });
  }

  onSelectionChanged(event) {
    const selectedRows = this.gridOptions.api.getSelectedRows();
    console.log(selectedRows);
    if (selectedRows.length > 0) {
      this.activateDelBtn = true;
    } else {
      this.activateDelBtn = false;
    }
    if (selectedRows.length === 1) {
      this.activateEditBtn = true;

    } else {
      this.activateEditBtn = false;

    }
  }

  loadGrid() {
    this.userService.authenticatedGet(ApiUrls.GetModel('inventoryitem'), {}).subscribe((res: any) => {
      this.rowData = res.data;
      this.gridOptions.api.setRowData(this.rowData);
     });
  }
  ngOnInit() {
    this.loadGrid();
    this.inventoryService.newinventoryItemObservable.subscribe(inventory => {
     this.rowData.push(inventory);
     this.gridOptions.api.setRowData(this.rowData);
   });
  }

  dateFormatter(params) {
    return moment(params.value).format('MM/DD/YYYY HH:mm');
  }


  ngAfterViewInit() {
    this.gridOptions.api.setColumnDefs([
    {headerName: 'Name', field: 'name', 'checkboxSelection': true, sortable: true, filter: true },
    {headerName: 'Barcode', field: 'barcode', sortable: true, filter: true},
    {headerName: 'Category', field: 'category', sortable: true, filter: true},
    {headerName: 'Type Of Dimension', field: 'dimensionType', sortable: true, filter: true},
    {headerName: 'Model', field: 'model', sortable: true, filter: true},
    {headerName: 'Saleable', field: 'saleable', sortable: true, filter: true},
    {headerName: 'Date Of Creation', field: 'time', sortable: true, filter: true, cellRenderer: (data) => {
      return moment(data.createdAt).format('MM/DD/YYYY HH:mm');
  }}
    ]);

    this.gridOptions.api.sizeColumnsToFit();
  }


}
