import { UserService } from 'src/app/services/authentication/user.service';
import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MatSnackBar } from '@angular/material';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ApiUrls } from '../../services/api.urls';
import { BusinessService } from 'src/app/services/business.service';

@Component({
// tslint:disable-next-line: component-selector
  selector: 'app-business-addLead',
  templateUrl: './business-addLead.component.html',
  styleUrls: ['./business-addLead.component.css']
})
export class BusinessAddLeadComponent implements OnInit {
  public registrationForm: FormGroup;

  constructor(public fb: FormBuilder, public dialogRef: MatDialogRef<BusinessAddLeadComponent>,
     public userService: UserService, private snackBar: MatSnackBar, private businessService: BusinessService) {
    this.registrationForm = fb.group({
      'name' : [null, Validators.required],
      'leadStage' : ['New', Validators.required],
      'address' : [null, Validators.required],
      'organisation' : [null, Validators.required],
      'leadSource' : [null, Validators.required],
   });
  }

  ngOnInit() {
  }

  registerLead(value: any, form): void {
   //  console.log(this.registrationForm.value);
   this.userService.authenticatedPost(ApiUrls.GetModel('leads'), JSON.stringify(this.registrationForm.value), {}).subscribe(
    res => {
      this.snackBar.open('Lead Added !', 'X', {
        duration: 4000,
        panelClass: ['success-snackbar'],
        verticalPosition: 'top'
      });
      this.businessService.emitLead(this.registrationForm.value);
      this.registrationForm.reset();
      this.dialogRef.close();
    },
    err => {
      console.log('Error occured');
      console.log(err);
      this.snackBar.open('Lead creation failed !', 'X', {
        duration: 4000,
        panelClass: ['error-snackbar'],
        verticalPosition: 'top'
      });
    }
  );
  }

  closeDialog() {
    this.dialogRef.close();
  }

}
