import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Process, Step } from 'src/app/_models/Process';
import { FormBuilder, FormGroup, FormArray } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-defineProductionRun',
  templateUrl: './defineProductionRun.component.html',
  styleUrls: ['./defineProductionRun.component.css']
})
export class DefineProductionRunComponent implements OnInit {
arrProcesses: any;
unSortedProcesses: any;
processSteps: Step[];
productData: any;
productId: string;
processData: Process;
form: FormGroup;
itemForm: FormGroup;
productForm: FormGroup;
additionalProductForm: FormGroup;
stepEdit: boolean[] = [true, true, true, true,  true, true, true, true, true, true,
  true, true, true, true, true, true, true, true, true];

  constructor(private httpService: HttpClient, private fb: FormBuilder, private fbItem: FormBuilder,
    private fbProduct: FormBuilder, private fbAdditionalProduct: FormBuilder, private router: Router) { }

  ngOnInit() {
    this.form = this.fb.group({
      steps: this.fb.array([])
    });
    this.itemForm = this.fbItem.group({
      newItems: this.fbItem.array([this.createNewItem()])
    });
    this.productForm = this.fbProduct.group({
      mainProducts: this.fbProduct.array([this.createProduct()])
    });
    this.additionalProductForm = this.fbAdditionalProduct.group({
      additionalProducts: this.fbAdditionalProduct.array([this.createAdditionalProduct()])
    });
   this.productData = JSON.parse(localStorage.getItem('productionForm'));
  this.httpService.get('./../../../assets/process.json').subscribe(
    data => {
      this.arrProcesses = data as Process[];
      this.unSortedProcesses = data;
      this.loadSteps();
    },
    (err: HttpErrorResponse) => {
      console.log (err.message);
    }
  );
  }

  loadSteps() {
    this.processData = this.arrProcesses.filter((process) => this.productData.product.includes(process.product))[0];
    this.productId = this.processData.productId;
    this.processSteps = this.processData.steps;
    for ( let i = 0; i < this.processSteps.length; i++) {
      this.addExisting(this.processSteps[i].name);
    }
  }



  addExistingItem(newItem) {
    return this.fb.group({
      step: [newItem]
    });
  }

  addExisting(newItem) {
    (this.form.controls['steps'] as FormArray).push(this.addExistingItem(newItem));
  }

  createNewItem() {
    return this.fbItem.group({
      item: ['']
    });
  }

  addNextItem() {
    (this.itemForm.controls['newItems'] as FormArray).push(this.createNewItem());
  }


  deleteRowItem(index: number) {
    (this.itemForm.controls['newItems'] as FormArray).removeAt(index);
  }


  createItem() {
    return this.fbItem.group({
      step: ['']
    });
  }
  addNextProduct() {
    (this.productForm.controls['mainProducts'] as FormArray).push(this.createProduct());
  }


  deleteRowProduct(index: number) {
    (this.productForm.controls['mainProducts'] as FormArray).removeAt(index);
  }

  createAdditionalProduct() {
    return this.fbAdditionalProduct.group({
      additionalProduct: ['']
    });
  }

  addNextAdditionalProduct() {
    (this.additionalProductForm.controls['additionalProducts'] as FormArray).push(this.createAdditionalProduct());
  }


  deleteRowAdditionalProduct(index: number) {
    (this.additionalProductForm.controls['additionalProducts'] as FormArray).removeAt(index);
  }

  createProduct() {
    return this.fbProduct.group({
      product: ['']
    });
  }

  addNext() {
    (this.form.controls['steps'] as FormArray).push(this.createItem());
  }


  deleteRow(index: number) {
    (this.form.controls['steps'] as FormArray).removeAt(index);
  }

  editRow(i) {
   this.stepEdit[i] = false;
   console.log(this.stepEdit);
  }

  submit() {
    try {
    localStorage.removeItem('stepForm');
    localStorage.removeItem('itemForm');
    localStorage.removeItem('productForm');
    localStorage.removeItem('additionalProductForm');
    } catch (e) {
      console.log(e);
    }
    localStorage.setItem('stepForm', JSON.stringify(this.form.value));
    localStorage.setItem('itemForm', JSON.stringify(this.itemForm.value));
    localStorage.setItem('productForm', JSON.stringify(this.productForm.value));
    localStorage.setItem('additionalProductForm', JSON.stringify(this.additionalProductForm.value));
  }

  goToReviewPage() {
    this.submit();
    this.router.navigate(['/reviewProduction']);
  }

}
