import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { AddSubAssemblyComponent } from '../AddSubAssembly/AddSubAssembly.component';

@Component({
  selector: 'app-AddAssembly',
  templateUrl: './AddAssembly.component.html',
  styleUrls: ['./AddAssembly.component.css']
})
export class AddAssemblyComponent implements OnInit {

  constructor(private dialog: MatDialog, public dialogRef: MatDialogRef<AddAssemblyComponent>) { }

  ngOnInit() {
  }

  addSubAssembly() {
  this.dialog.open(AddSubAssemblyComponent,  {
  width: '350px',
  });
  }

  closeDialog() {
    this.dialogRef.close();
  }

}
