import { LeadStage } from './../../_models/LeadStage';
import { MatSnackBar } from '@angular/material';
import { UserService } from 'src/app/services/authentication/user.service';
import { FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { select, json } from 'd3';
import { ApiUrls } from 'src/app/services/api.urls';

@Component({
  selector: 'app-business-settings',
  templateUrl: './business-settings.component.html',
  styleUrls: ['./business-settings.component.css']
})
export class BusinessSettingsComponent implements OnInit {
  public registrationForm: FormGroup;
  public leadStages: Array<LeadStage>;
  public editStageMode = false;
  public leadChangeTitle = 'Add New Lead Stage';
  public leadButtonTitle = 'Add Lead Stage';
  newLeadStage: any;
  mappingVariable: any;
  public newStage: LeadStage;
  public editStage: LeadStage;


  constructor(private userService: UserService, private snackBar: MatSnackBar) {
  }

  ngOnInit() {
     this.showExistingStages();
  }

  addLeadStage(operation: string): void {
    if (operation === 'edit') {
      this.userService.authenticatedPatch(
        ApiUrls.FromModelById('leadstage', this.editStage.id),
        JSON.stringify({
          userId: this.editStage.userId,
          time: this.editStage.time,
          Name: this.newLeadStage
        }), {}).subscribe((res) => {
      if (res.success) {
        // this.close();
        // this.rowData.data.meta = this.authForm.value.meta;
        this.snackBar.open('Updated', 'X', {
          duration: 2000,
        });
      } else {
        this.snackBar.open('Error: ' + res.error, 'X', {
          duration: 2000,
        });
      }
    });
    } else if (this.editStageMode === false) {
   this.mappingVariable = { Name : this.newLeadStage } ;
   console.log(this.mappingVariable);
   this.userService.authenticatedPost(ApiUrls.GetModel('leadstage'), JSON.stringify(this.mappingVariable), {}).subscribe(
    res => {
      this.snackBar.open('Lead Added !', 'X', {
        duration: 4000,
        panelClass: ['success-snackbar'],
        verticalPosition: 'top'
      });
      this.newLeadStage = '';
    },
    err => {
      console.log('Error occured');
      console.log(err);
      this.snackBar.open('Lead creation failed !', 'X', {
        duration: 4000,
        panelClass: ['error-snackbar'],
        verticalPosition: 'top'
      });
    }
  );
  this.newStage.Name = this.newLeadStage;
    this.leadStages.push(this.newStage);
    } else  {
      this.leadChangeTitle = 'Add New Lead Stage';
      this.leadButtonTitle = 'Add Lead Stage';
      this.newLeadStage = '';
      this.editStageMode = false;

    }
  }

  editLeadStage(selectedLeadStage: LeadStage) {
    console.log(selectedLeadStage);
this.editStageMode = true;
this.leadChangeTitle = 'Edit Lead Stage';
this.leadButtonTitle = 'Save Lead Stage';
this.newLeadStage = selectedLeadStage.Name;
this.editStage = selectedLeadStage;
  }

  deleteLeadStage(selectedLeadStage: any) {
    console.log(selectedLeadStage);
    this.userService.authenticatedDelete(ApiUrls.ByModel('leadstage' ,
    JSON.stringify({where: {id: selectedLeadStage.id }}))).subscribe((res) => {
      this.snackBar.open('Deleted !', 'X', {
        duration: 4000,
        panelClass: ['error-snackbar']
      });
    });
      }

  showExistingStages() {
    this.userService.authenticatedGet(ApiUrls.GetModel('leadstage'), {}).subscribe((res: any) => {
      this.leadStages = res.data as LeadStage[];
      console.log(this.leadStages);

    });
  }

}
