import { InventoryService } from './../../services/inventory.service';
import { map } from 'rxjs/operators';


import 'rxjs/add/operator/switchMap';

import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/services/authentication/user.service';
import { MatDialog, MAT_DIALOG_DATA, MatSnackBar } from '@angular/material';
import { InventoryEditComponent } from '../Inventory-Edit/Inventory-Edit.component';
import { GridOptions } from 'ag-grid-community';
import { RegisterInventoryComponent } from '../RegisterInventory/RegisterInventory.component';
import { Observable } from 'rxjs';
import { forEach } from '@angular/router/src/utils/collection';
import { ApiUrls } from 'src/app/services/api.urls';

@Component({
  selector: 'app-Inventory-Display',
  templateUrl: './Inventory-Display.component.html',
  styleUrls: ['./Inventory-Display.component.css']
})
export class InventoryDisplayComponent implements OnInit {
  public rowData: any;
  public inventoryData: any;
  public inventoryItemData: any;
  public rowSelection;
  public defaultColDef;
  public overlayNoRowsTemplate;
  gridOptions: GridOptions = { };
  public activateDelBtn = false;
  public activateEditBtn = false;


    columnDefs = [
    {headerName: 'Name', field: 'name', sortable: true, filter: true },
    {headerName: 'ItemId', field: 'itemId', sortable: true, filter: true },
    {headerName: 'Id', field: 'id', sortable: true, filter: true },
    {headerName: 'Quantity Amount', field: 'quantityAmount', sortable: true, filter: true},
    {headerName: 'Quantity Unit', field: 'quantityUnit', sortable: true, filter: true},
  ];

  constructor(public userService: UserService,  private snackBar: MatSnackBar, public dialog: MatDialog, 
    private inventoryService: InventoryService) {

     this.rowSelection = 'single';
     this.defaultColDef = { resizable: true };

     this.overlayNoRowsTemplate =
     '<span class="ag-overlay-loading-center">Please wait while your rows are loading</span>';
   }

   onEditSelected() {
    const selectedRows = this.gridOptions.api.getSelectedRows();
    console.log(selectedRows);
      this.dialog.open(InventoryEditComponent, {
       width: '450px',
        data: {data: selectedRows[0], activateEditBtn: true}
      });
  }

  onAddSelected() {
    this.dialog.open(RegisterInventoryComponent, {
      width: '350px',
     });
  }

  onRemoveSelected() {

    const selectedData = this.gridOptions.api.getSelectedRows();
    console.log(selectedData);
    if (confirm('Click OK to delete the selected rows')) {
      this.userService.authenticatedDelete(ApiUrls.GetModel('inventory') + selectedData[0].id.toString()).subscribe((res) => {
        this.snackBar.open('Deleted !', 'X', {
          duration: 4000,
          panelClass: ['error-snackbar']
        });
        this.gridOptions.api.updateRowData({ remove: selectedData });
      });
    }
  }

   getInventoryName(inventoryId) {
    return  this.inventoryItemData.filter(inventoryData => inventoryData.id === inventoryId)[0].name;

  }

  loadGrid() {
    this.userService.authenticatedGet(ApiUrls.GetModel('inventory'), {}).subscribe((res: any) => {
      this.rowData = res.data;
      console.log(this.rowData);
      this.finalCombine();
    });

  }

  finalCombine() {
    this.userService.authenticatedGet(ApiUrls.GetModel('inventoryitem'), {}).subscribe((res: any) => {
    this.inventoryItemData = res.data;
      this.inventoryData = this.rowData.map( inventory => {
        return {id: inventory.id, itemId: inventory.itemId, quantityAmount: inventory.quantityAmount ,
          quantityUnit: inventory.quantityUnit, name: this.getInventoryName(inventory.itemId)};
    });
    this.gridOptions.api.setRowData(this.inventoryData);
    });

  }

  onSelectionChanged(event) {
    const selectedRows = this.gridOptions.api.getSelectedRows();
    console.log(selectedRows);
    if (selectedRows.length > 0) {
      this.activateDelBtn = true;
    } else {
      this.activateDelBtn = false;
    }
    if (selectedRows.length === 1) {
      this.activateEditBtn = true;

    } else {
      this.activateEditBtn = false;

    }
  }

  ngOnInit() {
    this.loadGrid();
    this.inventoryService.newinventoryObservable.subscribe(res => {
      if (res !== null) {
      this.rowData.push(res);
      this.finalCombine();
      this.inventoryService.emitInventory(null);
      }
    });
  }

  ngAfterViewInit() {
    this.gridOptions.api.setColumnDefs([
    {headerName: 'Name', field: 'name', 'checkboxSelection': true, sortable: true, filter: true },
    {headerName: 'Quantity Amount', field: 'quantityAmount', sortable: true, filter: true},
    {headerName: 'Quantity Unit', field: 'quantityUnit', sortable: true, filter: true},
    ]);

    this.gridOptions.api.sizeColumnsToFit();
  }


}
