import { UserService } from './../../services/authentication/user.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormArray } from '@angular/forms';
import { ApiUrls } from 'src/app/services/api.urls';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-sendForProduction',
  templateUrl: './sendForProduction.component.html',
  styleUrls: ['./sendForProduction.component.css']
})
export class SendForProductionComponent implements OnInit {

  form: FormGroup;
  productForm: FormGroup;
  wastageForm: FormGroup;
  productDetails: any;
  stepForm: any;
  additionalProductForm: FormGroup;
  wastageDetails: any;
  additionalProductDetails: any;
  itemForm: any;
  productData: any;
  productionForm: any;

  constructor(private userService: UserService, private snackBar: MatSnackBar) { }

  ngOnInit() {
    this.productData = JSON.parse(localStorage.getItem('productionForm'));
    this.itemForm = JSON.parse(localStorage.getItem('itemForm'));
    this.productDetails = JSON.parse(localStorage.getItem('productForm'));
    this.stepForm = JSON.parse(localStorage.getItem('stepForm'));
    this.additionalProductDetails = JSON.parse(localStorage.getItem('additionalProductForm'));
    this.wastageDetails = JSON.parse(localStorage.getItem('wastageForm'));
    this.productionForm = JSON.parse(localStorage.getItem('productionForm'));
    console.log(this.productionForm);
  }

  enterFinalForm() {
   let finalForm = {
    'additionalProducts': this.additionalProductDetails,
    'mainProducts': this.productDetails,
    'steps': this.stepForm,
    'items': this.itemForm,
    'wastageItems': this.wastageDetails
    };
// console.log(this.stepForm);
var obj3 = Object.assign(this.productionForm, this.productDetails, this.additionalProductDetails,
   this.itemForm, this.stepForm, this.wastageDetails);
    let merged = {...this.productionForm, ...this.productDetails,  ...this.stepForm, ...this.itemForm,
       ...this.additionalProductDetails, ...this.wastageDetails};
    console.log(JSON.stringify(obj3));
    this.userService.authenticatedPost(ApiUrls.GetModel('finalprod'), JSON.stringify(obj3), {}).subscribe(
      res => {
        this.snackBar.open('Production Run Added !', 'X', {
          duration: 4000,
          panelClass: ['success-snackbar'],
          verticalPosition: 'top'
        });
        // this.registrationForm.reset();
      },
      err => {
        console.log('Error occured');
        console.log(err);
        this.snackBar.open('Production Run Creation Failed !', 'X', {
          duration: 4000,
          panelClass: ['error-snackbar'],
          verticalPosition: 'top'
        });
      }
    );
  }






}
