import { Component, Inject, forwardRef } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";

import { UserService } from "./user.service";

import {
  MAT_DIALOG_DATA,
  MatDialog,
  MatDialogRef,
  MatSnackBar
} from "@angular/material";

@Component({
  selector: "app-user-service-check-relogin",
  template: `
    <ng-container *ngIf="!loading">
      <h1 mat-dialog-title>Login expired, enter password again!</h1>
      <div mat-dialog-content>
        <section></section>
        <form
          class="loginForm"
          [formGroup]="loginForm"
          (ngSubmit)="doLogin($event)"
        >
          <section>
            <mat-form-field style="width:100%">
              <input
                matInput
                placeholder="Password"
                formControlName="password"
                type="password"
              />
            </mat-form-field>
            <mat-error *ngIf="error">
              {{ error | json }}
            </mat-error>
          </section>
        </form>
      </div>
      <div align="right">
        <button mat-button (click)="dialogRef.close('no')" color="warn">
          Cancel
        </button>
        <button
          mat-raised-button
          [disabled]="!loginForm.valid"
          (click)="doLogin($event)"
          color="primary"
        >
          Login
        </button>
      </div>
    </ng-container>
    <div
      mat-dialog-content
      align="center"
      *ngIf="loading"
      style="height:125px; width:125px; vertical-align: middle"
    >
      <mat-spinner></mat-spinner>
    </div>
  `
})
export class UserServiceCheckReloginDialogComponent {
  loading = false;
  error: any;
  public loginForm: FormGroup = this.fb.group({
    password: [
      "",
      [Validators.required, Validators.minLength(6), Validators.maxLength(32)]
    ]
  });
  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<UserServiceCheckReloginDialogComponent>,
    public fb: FormBuilder,
    @Inject(forwardRef(() => UserService)) public userService: UserService,
    private dialog: MatDialog
  ) {}
  doLogin(event): void {
    this.loading = true;
    this.userService
      .login({
        username: this.userService.userData.email,
        password: this.loginForm.value.password
      })
      .subscribe(
        data => {
          this.loading = false;
          if (!data.success) {
          } else {
            this.dialogRef.close("yes");
          }
        },
        error => {
          this.error = error.error.error
            ? error.error.error.message
              ? error.error.error.message
              : error.error.error
            : error.error.statusText;
          this.loading = false;
        }
      );
  }
}
