import { InventoryService } from './../../services/inventory.service';
import { InventoryCategory } from './../../_models/InventoryCategory';
import { UserService } from 'src/app/services/authentication/user.service';
import { Component, OnInit, Injectable } from '@angular/core';
import { InventoryItem } from '../../_models/InventoryItem';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {MatSnackBar, MatDialogRef} from '@angular/material';
import {MatChipInputEvent} from '@angular/material/chips';
import { ENTER, COMMA } from '@angular/cdk/keycodes';
import { ApiUrls } from 'src/app/services/api.urls';

export interface Unit {
  name: string;
}

@Component({
  selector: 'app-Register',
  templateUrl: './Register.component.html',
  styleUrls: ['./Register.component.css']
})

export class RegisterComponent implements OnInit {
  public registrationForm: FormGroup;
  public inventoryItem: InventoryItem;
  categories: InventoryCategory[];
  public httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  public dimension = ['Rectangular', 'Sheet', 'Pipe', 'Cylinder'];
  

  constructor(public fb: FormBuilder , private userService: UserService, private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<RegisterComponent>, private inventoryService: InventoryService) {
    this.registrationForm = fb.group({
      'name' : [null, Validators.required],
      'saleable' : [null, Validators.required],
      'barcode' : [null, Validators.required],
      'model' : [null, Validators.required],
      'category' : [null, Validators.required],
      'dimensionType' : [null, Validators.required],
   });
  }


  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  units: Unit[] = [
    {name: 'Kilograms'},
    {name: 'Grams'},
  ];

  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    // Add our fruit
    if ((value || '').trim()) {
      this.units.push({name: value.trim()});
    }

    // Reset the input value
    if (input) {
      input.value = '';
    }
  }

  remove(unit: Unit): void {
    const index = this.units.indexOf(unit);

    if (index >= 0) {
      this.units.splice(index, 1);
    }
  }


  registerInventoryItem(value: any, form): void {
    const inventoryNewItem: InventoryItem = this.registrationForm.value;
    this.userService.authenticatedPost(ApiUrls.GetModel('inventoryitem'),
      JSON.stringify({...Object.keys(this.registrationForm.value).filter(a =>
        this.registrationForm.value[a] !== null && this.registrationForm.value[a] !== '')
       .map(a => {
         if ( this.registrationForm.value[a] === 'true' || this.registrationForm.value[a] === 'false') {
             if ( this.registrationForm.value[a] === 'true')  {
              this.registrationForm.value[a] = true;
              return {[a] : this.registrationForm.value[a]};
             } else { this.registrationForm.value[a] = false;
            return {[a] : this.registrationForm.value[a]}; }
         } else {
         return {[a] : this.registrationForm.value[a]};
         }
       }).reduce((a, b) => {
         return {...a, ...b};
       }), roles: [this.registrationForm.value.role]})
      , {}).subscribe(res => {
        this.snackBar.open('Inventory Item successfully created !', 'X', {
          duration: 4000,
          panelClass: ['success-snackbar'],
          verticalPosition: 'top'
        });
        this.inventoryService.emitInventoryItem(this.registrationForm.value);
        this.registrationForm.reset();
        form.resetForm();
        this.closeDialog();
      },
      err => {
        console.log('Error occured');
        console.log(err);
        this.snackBar.open('Inventory Item creation failed !', 'X', {
          duration: 4000,
          panelClass: ['error-snackbar'],
          verticalPosition: 'top'
        });
      });

  }

  closeDialog() {
    this.dialogRef.close();
  }

  ngOnInit() {
    this.userService.authenticatedGet(ApiUrls.GetModel('inventorycategory'), {}).subscribe((res: any) => {
      this.categories = res.data as InventoryCategory[];
      console.log(res);
    });
  }

}
