import { Component, OnInit, Input } from '@angular/core';
import { UserService } from '../../../services/authentication/user.service';
import { ApiUrls } from '../../../services/api.urls';
import { MemCacheService } from '../../../services/cache/mem-cache.service';

@Component({
  selector: 'app-print-mandal',
  templateUrl: './print-mandal.component.html',
  styleUrls: ['./print-mandal.component.css']
})
export class PrintMandalComponent implements OnInit {
  public name = '';
  private stateMandalId;
  public loading = false;
  @Input('stateMandalId') set _stateMandalId(val: String) {
    if (val === '') {
      this.stateMandalId = '';
      this.name = 'Not available';
    } else if (this.stateMandalId !== val) {
      this.stateMandalId = val;
      this.load();
    }
  }
  constructor(
    private userService: UserService,
    private memCache: MemCacheService
  ) {}

  ngOnInit() {}

  load() {
    this.loading = true;
    this.memCache.getItem('statemandal', this.stateMandalId).map((d) => {
      this.loading = false;
      return d;
    }).catch((d) => {
      this.loading = false;
      return d;
    }).subscribe(
      data => {
        this.name = data.name;
      },
      err => {
        this.name = 'Not available';
      }
    );
  }
}
