import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { UserService } from '../authentication/user.service';
import { ApiUrls } from '../api.urls';
import { OK } from 'http-status-codes';

export interface DataCache<T> {
  data: T;
  time: Number;
}

@Injectable({
  providedIn: 'root'
})
export class MemCacheService {
  public dataCache: Map<string, DataCache<any>> = new Map();
  public dataLoading: Map<string, ReplaySubject<any>> = new Map();
  constructor(private userService: UserService) {}

  getItem(modelName: string, id: string): Observable<any> {
    const dataId = modelName + id;
    if (this.dataCache.has(dataId)) {
      const cache = this.dataCache.get(dataId);
      const time = new Date().getTime();
      if (cache.time > time - 600000) {
        return Observable.of(cache.data);
      } else {
        this.dataLoading.delete(dataId);
        return this._load(modelName, id);
      }
    } else {
      return this._load(modelName, id);
    }
  }
  private _load(modelName: string, id: string) {
    const dataId = modelName + id;
    if (this.dataLoading.has(dataId)) {
      return this.dataLoading.get(dataId).asObservable();
    } else {
      const rs = new ReplaySubject<any>(1);
      this.dataLoading.set(dataId, rs);
      this.userService
        .authenticatedGet(ApiUrls.FromModelById(modelName, id), {}, OK, true)
        .subscribe(
          res => {
            if (res.success) {
              this.dataCache.set(dataId, <DataCache<any>>{
                data: res.data,
                time: new Date().getTime()
              });
              rs.next(res.data);
            } else {
              rs.error('Not found');
            }
          },
          error => {
            rs.error(error);
          }
        );
      return rs.asObservable();
    }
  }
}
