import { Component, Input, OnInit, AfterViewInit, AfterContentInit, AfterContentChecked,
   AfterViewChecked, ViewChild, Output, EventEmitter } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ApiUrls } from '../../../services/api.urls';
import { UserService } from '../../../services/authentication/user.service';

import {Observable} from 'rxjs';
import {debounceTime, switchMap} from 'rxjs/operators';
import { MemCacheService } from 'src/app/services/cache/mem-cache.service';


@Component({
  selector: 'app-app-autocomplete',
  templateUrl: './app-autocomplete.component.html',
  styleUrls: ['./app-autocomplete.component.css']
})
export class AppAutocompleteComponent implements OnInit, AfterViewInit, AfterContentInit {
  @Input('model') model: string;

  @Input('label') label: string;


  shallIReset = false;
  _filter: string = JSON.stringify({});
  timeout = null;

  displayFn = this.displayFunction.bind(this);

  @Input('filter') set filter(val: Object) {
    const json = JSON.stringify(val);
    if (this._filter !== json) {
      this._filter = json;
      clearTimeout(this.timeout);
      if (this.internalControl && new Date().getTime() - 1600 > this.startDate.getTime()) {
        setTimeout(() => this.internalControl.reset(), 0);
      }
      // this.timeout = setTimeout(this.load.bind(this), 150);
    }
  }

  @Input('formControl') internalControl: FormControl;


  // internalControl = new FormControl();

  items: Array < { id, name } > = [];

  filteredOptions;

  defaultVal;

  startDate: Date;

  constructor(private userService: UserService, private memCacheService: MemCacheService) {
    this.startDate = new Date();
  }

  ngOnInit() {
    setTimeout(() => {
      this.shallIReset = true;
    }, 10000);
    this.filteredOptions = this.internalControl.valueChanges.pipe(
        debounceTime(300),
        switchMap(value => this.searchFilter(value))
    );
    /*
    const reset = this.internalControl.reset;
    this.internalControl.reset = (r) => {
      reset({value: this.defaultVal});
    };*/
  }

  ngAfterViewInit() {
  }

  ngAfterContentInit() {
    this.load();
  }

  private searchFilter(event): Observable<any[]> {
    const filterValue = event; // .target.value.toLowerCase();

    const currentFilter = JSON.parse(this._filter);
    if (currentFilter.where === undefined) {
      currentFilter['where'] = {};
    }
    currentFilter['where']['name']  =  {$regex: `^.*${filterValue}.*`, $options: 'i'};
    const ret = this.userService.authenticatedGet(ApiUrls.ByModel(this.model, JSON.stringify(currentFilter)))
      .map((res): any[] => {
        if (res.success) {
          this.items = res.data;
          return this.items;
        } else {
          return [];
        }
      });
      return ret;
  }
  private getDefaultValue() {
    switch (this.model) {
      case 'state':
        return this.userService.userData.stateId;
        break;
      case 'statedistrict':
        return this.userService.userData.stateDistrictId;
        break;
      case 'stateconstituency':
        return this.userService.userData.stateConstituencyId;
        break;
      case 'statemandal':
        return this.userService.userData.stateMandalId;
        break;
      default:
        return null;
        break;
    }
  }

  private load() {
    this.userService
    .getCurrentUser()
    .subscribe(data => {
      const id = this.getDefaultValue();
      this.defaultVal = this.internalControl.value && this.internalControl.value !== '' ? this.internalControl.value : id;
      if (!this.defaultVal) {
        return;
      }
      this.memCacheService.getItem(this.model, this.defaultVal)
      .subscribe(item => {
          setTimeout(() => {
            this.items.push(item);
            this.internalControl.patchValue(this.defaultVal, {emitEvent: false});
            // this.filteredOptions  = Observable.of(this.items);
          }, 0);
        });
    });
  }

  displayFunction(id ?: string): string | undefined {
    // console.log(this);
    const item = this.items.find(a => a.id === id);
    return item ? item.name : undefined;
  }
}
