import { Component, OnInit } from '@angular/core';
import { Step } from 'src/app/_models/Process';
import { Router } from '@angular/router';

@Component({
  selector: 'app-reviewProductionReport',
  templateUrl: './reviewProductionReport.component.html',
  styleUrls: ['./reviewProductionReport.component.css']
})
export class ReviewProductionReportComponent implements OnInit {
  itemForm: any;
  productForm: any;
  stepForm: any;
  additionalProductForm: any;
  productData: any;

  constructor(private router: Router) { }

  ngOnInit() {
    this.productData = JSON.parse(localStorage.getItem('productionForm'));
    this.itemForm = JSON.parse(localStorage.getItem('itemForm'));
    this.productForm = JSON.parse(localStorage.getItem('productForm'));
    this.stepForm = JSON.parse(localStorage.getItem('stepForm'));
    this.additionalProductForm = JSON.parse(localStorage.getItem('additionalProductForm'));

  }

  enterFinalForm() {
    this.router.navigate(['/reviewProductionRun']);
  }

  routeToFrontPage() {
    this.router.navigate(['/createProductionRun']);
  }

}
