import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class InventoryService {
  public inventoryObservable = new Subject<any>();
  public newinventoryObservable = new Subject<any>();
  public newinventoryItemObservable = new Subject<any>();
  public inventoryCategoryEditObservable = new Subject<any>();

constructor() { }

emitProperties(val) {
  this.inventoryObservable.next(val);
}

emitInventory(val) {
  this.newinventoryObservable.next(val);
}

emitInventoryItem(val) {
  this.newinventoryItemObservable.next(val);
}

emitInventoryCategoryEdit(val) {
  console.log('reached here');
  this.inventoryCategoryEditObservable.next(val);
}

}
