import * as $ from 'jquery';
import {
  CommonModule,
  LocationStrategy,
  HashLocationStrategy
} from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { PERFECT_SCROLLBAR_CONFIG } from 'ngx-perfect-scrollbar';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';

import {
  MAT_DIALOG_DEFAULT_OPTIONS, MatProgressSpinnerModule, MatAutocompleteModule, MatFormFieldModule, MatInputModule
} from '@angular/material';

import { AppState } from '../../manage/email/app.state';

import { PrintStateComponent } from '../utils/print-state/print-state.component';
import { PrintDistrictComponent } from '../utils/print-district/print-district.component';
import { PrintConstituencyComponent } from '../utils/print-constituency/print-constituency.component';
import { PrintMandalComponent } from '../utils/print-mandal/print-mandal.component';

import { ApiUrls } from 'src/app/services/api.urls';
import { SerivcesModule } from 'src/app/services/services.module';
import { PrintBoothComponent } from './print-booth/print-booth.component';
import { FiltersComponent } from './filters/filters.component';
import { AppAutocompleteComponent } from './app-autocomplete/app-autocomplete.component';


const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  suppressScrollX: true,
  wheelSpeed: 1,
  wheelPropagation: true,
  minScrollbarLength: 20
};
@NgModule({
  declarations: [
    PrintStateComponent,
    PrintDistrictComponent,
    PrintConstituencyComponent,
    PrintMandalComponent,
    PrintBoothComponent,
    FiltersComponent, AppAutocompleteComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    HttpClientModule, ReactiveFormsModule,
    SerivcesModule, MatProgressSpinnerModule, MatFormFieldModule, MatAutocompleteModule, MatInputModule
  ],
  exports: [PrintStateComponent, PrintDistrictComponent,
     PrintConstituencyComponent, PrintMandalComponent, PrintBoothComponent, AppAutocompleteComponent, FiltersComponent],
  providers: [
    {
      provide: PERFECT_SCROLLBAR_CONFIG,
      useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
    },
    {
      provide: LocationStrategy,
      useClass: HashLocationStrategy
    },
    { provide: MAT_DIALOG_DEFAULT_OPTIONS, useValue: { hasBackdrop: false } },
    [AppState],
    ApiUrls
  ],
  entryComponents: [
  ]
})
export class UtilsModule {}
